import React, { useState, useContext } from "react";
import {
  IconButton,
  Toolbar,
  Typography,
  Hidden,
  Drawer,
  CssBaseline,
  AppBar,
  Box,
  Tooltip,
  Container,
} from "@material-ui/core";

import { MenuRounded, PowerSettingsNewRounded } from "@material-ui/icons";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import DrawerSide from "./Drawer";

import { Link as RouterLink } from "react-router-dom";

import AuthContext from "../../context/auth/authContext";
import Notification from "./Notification";

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
  },
  // "@global": {
  //   "*::-webkit-scrollbar": {
  //     width: "0.4em",
  //     height: "0.4em",
  //     backgroundColor: "rgba(50,50,50,0.00)",
  //   },
  //   "*::-webkit-scrollbar-track": {
  //     "-webkit-box-shadow": "inset 0 0 1px rgba(50,50,50,1.0)",
  //   },
  //   "*::-webkit-scrollbar-thumb": {
  //     backgroundColor: theme.palette.primary.main,
  //     outline: "2px solid slategrey",
  //   },
  // },
  drawer: {
    [theme.breakpoints.up("sm")]: {
      width: drawerWidth,
      flexShrink: 0,
    },
  },
  title: { color: "white", textDecoration: "none" },
  appBar: {
    [theme.breakpoints.up("sm")]: {
      width: `calc(100% - ${drawerWidth}px)`,
      marginLeft: drawerWidth,
    },
  },
  menuButton: {
    marginRight: theme.spacing(2),
    [theme.breakpoints.up("sm")]: {
      display: "none",
    },
  },
  // necessary for content to be below app bar
  toolbar: theme.mixins.toolbar,
  drawerPaper: {
    backgroundColor: theme.palette.primary.dark,
    width: drawerWidth,
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
  },
}));

function DashboardWrapper(props) {
  const authContext = useContext(AuthContext);
  const { selectedIndex, selectedSubIndex } = props;
  const { window } = props;
  const DrawerObj = DrawerSide();

  const classes = useStyles();
  const theme = useTheme();

  const [mobileOpen, setMobileOpen] = useState(false);

  const handleDrawerToggle = (value) => {
    setMobileOpen(value);
  };

  const container =
    window !== undefined ? () => window().document.body : undefined;

  if (authContext.user && authContext.user.role !== "society")
    return (
      <div className={classes.root}>
        <CssBaseline />
        <AppBar position="fixed">
          <Toolbar>
            <Box width="100%">
              <Typography
                component={RouterLink}
                to="/"
                variant="h6"
                noWrap
                className={classes.title}
              >
                Ministry of Labour and Employment
              </Typography>
            </Box>
            <Tooltip title="Logout">
              <IconButton
                onClick={() => {
                  authContext.logout();
                }}
              >
                <Box color="primary.contrastText">
                  <PowerSettingsNewRounded />
                </Box>
              </IconButton>
            </Tooltip>
          </Toolbar>
        </AppBar>
        <main className={classes.content}>
          <div className={classes.toolbar} />
          <Container>{props.children}</Container>
        </main>
      </div>
    );

  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar position="fixed" className={classes.appBar}>
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={() => handleDrawerToggle(true)}
            className={classes.menuButton}
          >
            <MenuRounded />
          </IconButton>
          <Box width="100%">
            <Typography variant="h6" noWrap>
              {authContext.user && authContext.user.name}
            </Typography>
          </Box>
          <Notification color="primary.contrastText" />
          <Tooltip title="Logout">
            <IconButton
              onClick={() => {
                authContext.logout();
              }}
            >
              <Box color="primary.contrastText">
                <PowerSettingsNewRounded />
              </Box>
            </IconButton>
          </Tooltip>
        </Toolbar>
      </AppBar>
      <nav className={classes.drawer} aria-label="mailbox folders">
        {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
        <Hidden smUp implementation="js">
          <Drawer
            container={container}
            variant="temporary"
            anchor={theme.direction === "rtl" ? "right" : "left"}
            open={mobileOpen}
            onClose={() => handleDrawerToggle(false)}
            classes={{
              paper: classes.drawerPaper,
            }}
            ModalProps={{
              keepMounted: true, // Better open performance on mobile.
            }}
          >
            <DrawerObj
              {...{ selectedIndex, selectedSubIndex, handleDrawerToggle }}
            />
          </Drawer>
        </Hidden>
        <Hidden xsDown implementation="css">
          <Drawer
            classes={{
              paper: classes.drawerPaper,
            }}
            variant="permanent"
            open
          >
            <DrawerObj
              {...{ selectedIndex, selectedSubIndex, handleDrawerToggle }}
            />
          </Drawer>
        </Hidden>
      </nav>
      <main className={classes.content}>
        <div className={classes.toolbar} />
        {props.children}
      </main>
    </div>
  );
}

export default DashboardWrapper;
