import React, { useReducer } from "react";
import FileContext from "./fileContext";
import fileReducer from "./fileReducer";

import axios from "axios";
import setAuthToken from "../../../utils/setAuthToken";

import {
  GET_FILES,
  POST_FILES,
  DELETE_FILE,
  FILE_ERROR,
  CLEAR_ERROR,
  GET_RESOURCES,
  RESOURCE_ERROR,
} from "../types";

const config = {
  headers: {
    "Content-Type": "application/json",
  },
};

const FileState = (props) => {
  const initialState = {
    loading: false,
    files: [],
    resources: [],
    fileError: null,
  };
  const [state, dispatch] = useReducer(fileReducer, initialState);

  const getResources = async () => {
    try {
      const res = await axios.get("/api/resources");
      console.log("Response..", res.data);
      dispatch({
        type: GET_RESOURCES,
        payload: res.data,
      });
    } catch (error) {
      dispatch({
        type: RESOURCE_ERROR,
        payload:
          (error &&
            error.response &&
            (error.response.data.msg || error.response.statusText)) ||
          "Runtime Error",
      });
      setTimeout(() => {
        clearError();
      }, 500);
    }
  };
  const getFiles = async () => {
    if (localStorage.token) {
      setAuthToken(localStorage.token);
    }
    try {
      const res = await axios.get("/api/user/files");
      dispatch({
        type: GET_FILES,
        payload: res.data,
      });
    } catch (error) {
      dispatch({
        type: FILE_ERROR,
        payload: error.response.data.msg || error.response.statusText,
      });
      setTimeout(() => {
        clearError();
      }, 500);
    }
  };
  const postFile = async (fileData) => {
    if (localStorage.token) {
      setAuthToken(localStorage.token);
    }
    try {
      const res = await axios.post("/api/user/files", fileData, config);
      dispatch({
        type: POST_FILES,
        payload: res.data,
      });
      getFiles();
    } catch (error) {
      dispatch({
        type: FILE_ERROR,
        payload: error.response.data.msg || error.response.statusText,
      });
      setTimeout(() => {
        clearError();
      }, 500);
    }
  };
  const deleteFile = async (fileurl) => {
    if (localStorage.token) {
      setAuthToken(localStorage.token);
    }
    try {
      const res = await axios.post("/api/user/files/delete", fileurl);
      dispatch({
        type: DELETE_FILE,
        payload: res.data,
      });
      getFiles();
    } catch (error) {
      dispatch({
        type: FILE_ERROR,
        payload: error.response.data.msg || error.response.statusText,
      });
      setTimeout(() => {
        clearError();
      }, 500);
    }
  };

  const clearError = () => {
    dispatch({ type: CLEAR_ERROR });
  };

  const uploadResources = async (data) => {
    console.log(data);
    if (localStorage.token) {
      setAuthToken(localStorage.token);
    }
    try {
      const res = await axios.post("/api/resources", data, config);
      dispatch({
        type: POST_FILES,
      });
    } catch (error) {
      console.log(error);
      dispatch({
        type: FILE_ERROR,
      });
    }
  };
  return (
    <FileContext.Provider
      value={{
        getFiles,
        deleteFile,
        postFile,
        uploadResources,
        getResources,
        loading: state.loading,
        files: state.files,
        fileError: state.fileError,
        resources: state.resources,
      }}
    >
      {props.children}
    </FileContext.Provider>
  );
};

export default FileState;
