import React, { useState, useContext, useEffect, Fragment } from "react";
import PageWrapper from "../Wrapper/PageWrapper";
import { SelectObj, FileButton } from "../FormItems";
import MembersTable from "../MembersTable";
import AlertContext from "../../context/alert/alertContext";
import AuthContext from "../../context/auth/authContext";
import axios from "axios";

import {
  Grid,
  TextField,
  Box,
  Container,
  FormControlLabel,
  Switch,
  Button,
  Typography,
  Collapse,
  Fab,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  DialogContentText,
} from "@material-ui/core";
import { PersonAddRounded } from "@material-ui/icons";
import { KeyboardDatePicker } from "@material-ui/pickers";
import { useHistory, Redirect } from "react-router-dom";
import { Profiler } from "react";
import Dashboard from "../../routing/Dashboard";

const options = [
  "Consumer",
  "Credit",
  "Agricultural/Farmers",
  "Housing",
  "Fishing",
  "Multipurpose",
  "Producers",
  "Marketing",
  "Worker",
  "Purchasing service",
  "Manufacturing",
  "Insurance",
  "Retail",
  "Others",
];

const states = [
  "Abia",
  "Adamawa",
  "Akwa Ibom",
  "Anambra",
  "Bauchi",
  "Bayelsa",
  "Benue",
  "Borno",
  "Cross River",
  "Delta",
  "Ebonyi",
  "Edo",
  "Ekiti",
  "Enugu",
  "Gombe",
  "Imo",
  "Jigawa",
  "Kaduna",
  "Kano",
  "Katsina",
  "Kebbi",
  "Kogi",
  "Kwara",
  "Lagos",
  "Nasarawa",
  "Niger",
  "Ogun",
  "Ondo",
  "Osun",
  "Oyo",
  "Plateau",
  "Rivers",
  "Sokoto",
  "Taraba",
  "Yobe",
  "Zamfara",
  "Federal Capital Territory",
];

const industryOptions = [];
options.map((option, index) =>
  industryOptions.push({ value: option, label: option })
);

const SocietyUpdate = () => {
  const authContext = useContext(AuthContext);
  const alertContext = useContext(AlertContext);
  const history = useHistory();
  const { setAlert } = alertContext;
  const [page, setPage] = useState(1);
  const [loading, setLoading] = useState(false);
  const [profile, setProfile] = useState({
    name: "",
    newAccount: false,
    type: "",
    regno: "",
    industry: "",
    address: "",
    roadStreet: "",
    lga: "",
    state: "",
    phone: "",
    purpose: "",
    lastElection: new Date(),
    lastAudit: new Date(),
    lastAuditYear: new Date(),
    auditClass: "",
    paidUpShareCapital: "0",
    membersList: [],
    files: {},
  });
  const {
    name,
    newAccount,
    type,
    regno,
    industry,
    address,
    roadStreet,
    lga,
    state,
    purpose,
    phone,
    lastAudit,
    lastElection,
    lastAuditYear,
    auditClass,
    paidUpShareCapital,
    membersList,
    files,
  } = profile;

  const uploadProfile = async () => {
    setLoading(true);
    console.log("Uploading...");
    const config = {
      headers: {
        Accept: "application/json",
        "Content-Type": "multipart/form-data",
      },
    };
    try {
      const cloud = process.env.REACT_APP_CLOUD;
      const form = new FormData();
      form.append("upload_preset", process.env.REACT_APP_UPLOAD_PRESET);
      form.append("cloud_name", cloud);
      form.append("folder", process.env.REACT_APP_FOLDER);
      console.log("Form out...");
      const upload_url = `https://api.cloudinary.com/v1_1/${cloud}/auto/upload`;
      const fileObj = [];
      const keys = Object.keys(files);
      for (let index = 0; index < keys.length; index++) {
        form.append("file", files[keys[index]]);
        await fetch(upload_url, {
          method: "POST",
          body: form,
        })
          .then((res) => res.json())
          .then((response) => {
            console.log(response.secure_url, "response");
            fileObj.push({ url: response.secure_url, propName: keys[index] });
          });
      }

      const list = [];
      const labelMap = {
        name: "Name",
        newAccount: "New Account",
        regno: "Registration Number",
        type: "Type",
        industry: "Industry",
        address: "Address",
        roadStreet: "Road/Street",
        lga: "L.G.A.",
        state: "State",
        phone: "Contact Phone Number",
        lastAudit: "Last Audit",
        lastElection: "Last Election",
        lastAuditYear: "Last Audit Year",
        auditClass: "Audit Class",
        paidUpShareCapital: "Paid Share Per Capital",
        membersList: "Members List",
        certificate: "Registration Certificate",
        activities: "Annual Report of Activities",
        profitandloss: "Profit And Loss",
        balancesheet: "Balance Sheet",
        bylaws: "Bylaws",
        feasibilityStudy: "Feasibility study/ Business Plan",
      };
      Object.keys(profile).map((item) => {
        console.log("item...", item);
        let object;
        switch (item) {
          case "lastAudit":
          case "lastElection":
          case "lastAuditYear":
            object = {
              type: "date",
              value: profile[item],
              propName: item,
              label: labelMap[item],
            };
            break;
          case "certificate":
          case "activities":
          case "profitandloss":
          case "balancesheet":
          case "bylaws":
          case "feasibilityStudy":
            break;
          case "newAccount":
            object = {
              type: "text",
              value: profile[item] ? "Yes" : "No",
              propName: item,
              label: labelMap[item],
            };
            break;
          case "membersList":
            object = {
              type: "table",
              value: profile[item],
              propName: item,
              label: labelMap[item],
            };
            break;
          default:
            object = {
              type: "text",
              value: profile[item],
              propName: item,
              label: labelMap[item],
            };
            break;
        }
        object && list.push(object);
      });
      fileObj.map((file) => {
        list.push({
          type: "file",
          value: file.url,
          propName: file.propName,
          label: labelMap[file.propName],
        });
      });
      console.log("list...", list);
      const res = await axios.post("/api/requests", {
        title: "Profile Update",
        description: "Society profile edited",
        content: list,
        changeProfile: true,
        toAdmin: true,
      });
      console.log("res", res.data);
      history.push("/society/requests");
    } catch (err) {
      console.log(err);
    }
    setLoading(false);
  };

  const setDetails = (e) => {
    setProfile({
      ...profile,
      [e.target.id || e.target.name]: e.target.value,
    });
  };

  const [page1Valid, setPage1Valid] = useState(false);
  const [page2Valid, setPage2Valid] = useState(false);
  useEffect(() => {
    setPage1Valid(
      name !== "" &&
        industry !== "" &&
        type !== "" &&
        address !== "" &&
        roadStreet !== "" &&
        state !== "" &&
        phone.length > 8 &&
        purpose !== "" &&
        (newAccount ||
          (!newAccount && regno !== "" && paidUpShareCapital !== ""))
    );
  }, [
    name,
    industry,
    address,
    roadStreet,
    state,
    purpose,
    phone,
    newAccount,
    regno,
    paidUpShareCapital,
    lastElection,
  ]);

  const [openAddMember, setOpenAddMember] = useState(false);

  const handleOpen = () => {
    setOpenAddMember(true);
  };
  const [openDelete, setOpenDelete] = useState(false);
  const [memberDelete, setMemberDelete] = useState("");
  const [indexDelete, setIndexDelete] = useState(-1);

  const deleteUser = (index) => {
    setMemberDelete(membersList[index].name);
    setIndexDelete(index);
    setOpenDelete(true);
  };
  const handleDeleteCancel = () => {
    setOpenDelete(false);
    setIndexDelete(-1);
    setMemberDelete("");
  };
  const handleDelete = () => {
    setProfile({
      ...profile,
      membersList: membersList.filter((member, index) => index !== indexDelete),
    });
    handleDeleteCancel();
  };

  const AddMember = (props) => {
    const { open, setOpenAddMember } = props;
    const [errormessage, setErrormessage] = useState("");
    const [member, setMember] = useState({
      name: "",
      mail: "",
      phone: "",
      role: "",
    });
    const { name, mail, phone, role } = member;
    const textChange = (e) => {
      setMember({ ...member, [e.target.id || e.target.name]: e.target.value });
    };
    const handleClose = () => {
      setOpenAddMember(false);
    };
    const handleAdd = () => {
      if (name === "" || mail === "" || phone === "" || role === "") {
        setErrormessage("Please fill in all the fields");
        setTimeout(() => {
          setErrormessage("");
        }, 2000);
      } else {
        setProfile({ ...profile, membersList: [...membersList, member] });
        setMember({ name: "", mail: "", phone: "", role: "" });
        handleClose();
      }
    };
    const roles = ["Chairman", "Secretary", "Official", "Member"].map(
      (role) => {
        return { value: role, label: role };
      }
    );
    const page = Dashboard();
    if (authContext.userRole && authContext.userRole !== "society")
      return <Redirect to={page} />;
    return (
      <Dialog open={open}>
        <DialogTitle>
          <Typography variant="h5" color="initial">
            New Member
          </Typography>
          <Collapse in={errormessage !== ""} collapsedHeight={0}>
            <Typography variant="subtitle1" color="error">
              <Box my={1}>{errormessage}</Box>
            </Typography>
          </Collapse>
        </DialogTitle>
        <DialogContent>
          <Grid container spacing={3}>
            <Grid item xs={12} md={6}>
              <TextField
                id="name"
                label="Name"
                variant="outlined"
                value={name}
                onChange={textChange}
                fullWidth
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <TextField
                id="mail"
                label="Email"
                variant="outlined"
                value={mail}
                onChange={textChange}
                fullWidth
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <TextField
                id="phone"
                label="Phone Number"
                variant="outlined"
                value={phone}
                onChange={textChange}
                fullWidth
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <SelectObj
                id="role"
                name="role"
                fullWidth
                label="Role"
                value={role}
                onChange={textChange}
                defaultValue=""
                options={roles}
              />
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions>
          <Button variant="text" color="secondary" onClick={handleAdd}>
            Add
          </Button>
          <Button variant="text" color="secondary" onClick={handleClose}>
            Cancel
          </Button>
        </DialogActions>
      </Dialog>
    );
  };
  const page1 = (
    <Grid container justify="center" spacing={3} alignItems="stretch">
      <Box textAlign="left">
        <Typography variant="body1" color="initial" gutterBottom>
          Please fill in all the fields with the correct information. If this is
          not your first time please indicate on the form.
        </Typography>
      </Box>
      <Grid item xs={12}>
        <TextField
          id="name"
          fullWidth
          label="Full Corporate Name"
          value={name}
          onChange={setDetails}
          variant="outlined"
        />
      </Grid>
      <Grid item xs={12}>
        <TextField
          id="phone"
          fullWidth
          label="Contact Phone Number"
          value={phone}
          onChange={setDetails}
          variant="outlined"
        />
      </Grid>
      <Grid item xs={12}>
        <TextField
          id="purpose"
          label="Purpose for creating society"
          value={purpose}
          fullWidth
          onChange={setDetails}
          variant="outlined"
        />
      </Grid>
      <Grid item xs={12}>
        <SelectObj
          id="industry"
          fullWidth
          label="Industry"
          value={industry}
          onChange={setDetails}
          defaultValue=""
          options={industryOptions}
        />
      </Grid>
      <Grid item xs={12}>
        <SelectObj
          id="type"
          fullWidth
          label="Type"
          value={type}
          onChange={setDetails}
          defaultValue=""
          options={["Primary", "Secondary", "Tertiary"].map((option) => {
            return { value: option, label: option };
          })}
        />
      </Grid>
      <Grid item xs={12}>
        <TextField
          id="address"
          fullWidth
          label="Address"
          value={address}
          onChange={setDetails}
          variant="outlined"
        />
      </Grid>
      <Grid item xs={12}>
        <TextField
          id="roadStreet"
          fullWidth
          label="Road/Street"
          value={roadStreet}
          onChange={setDetails}
          variant="outlined"
        />
      </Grid>
      <Grid item xs={12} md={6}>
        <TextField
          id="lga"
          label="L.G.A."
          value={lga}
          fullWidth
          onChange={setDetails}
          variant="outlined"
        />
      </Grid>
      <Grid item xs={12} md={6}>
        <SelectObj
          id="state"
          label="State"
          value={state}
          onChange={setDetails}
          defaultValue="state"
          options={states.map((state) => {
            return { value: state, label: state };
          })}
        />
      </Grid>

      <Grid item xs={12}>
        <KeyboardDatePicker
          id="lastElection"
          name="lastElection"
          label="Last Election"
          value={lastElection}
          placeholder="10/10/2018"
          onChange={(date) => setProfile({ ...profile, lastElection: date })}
          maxDate={new Date()}
          fullWidth
          inputVariant="outlined"
          format="DD/MM/yyyy"
        />
      </Grid>
      <Grid item xs={12}>
        <FormControlLabel
          control={
            <Switch
              checked={newAccount}
              onChange={(e) =>
                setProfile({ ...profile, newAccount: e.target.checked })
              }
              name="newAccount"
            />
          }
          label={newAccount ? "First time account" : "Returning account"}
        />
      </Grid>

      <Collapse in={!newAccount} collapsedHeight={0}>
        <Box mx={1.5}>
          <Grid container justify="center" spacing={3} alignItems="stretch">
            <Grid item xs={12} md={6}>
              <TextField
                id="regno"
                fullWidth
                label="Registration Number"
                value={regno}
                onChange={setDetails}
                variant="outlined"
              />
            </Grid>

            <Grid item xs={12} md={6}>
              <SelectObj
                id="auditClass"
                fullWidth
                label="Audit Class"
                value={auditClass}
                onChange={setDetails}
                defaultValue="state"
                options={["A", "B", "C", "D"].map((letter) => {
                  return { value: "Class " + letter, label: "Class " + letter };
                })}
              />
            </Grid>

            <Grid item xs={12}>
              <TextField
                id="paidUpShareCapital"
                label="Paid Share Per Capital"
                variant="outlined"
                value={paidUpShareCapital}
                onChange={setDetails}
                fullWidth
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <KeyboardDatePicker
                id="lastAudit"
                name="lastAudit"
                label="Last audit date"
                value={lastAudit}
                placeholder="10/10/2018"
                onChange={(date) => setProfile({ ...profile, lastAudit: date })}
                maxDate={new Date()}
                fullWidth
                inputVariant="outlined"
                format="DD/MM/yyyy"
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <KeyboardDatePicker
                id="lastAuditYear"
                name="lastAuditYear"
                label="For the year"
                value={lastAuditYear}
                placeholder="2020"
                onChange={(date) =>
                  setProfile({ ...profile, lastAuditYear: date })
                }
                maxDate={
                  new Date(new Date().setFullYear(new Date().getFullYear() + 1))
                }
                fullWidth
                inputVariant="outlined"
                format="yyyy"
                views={["year"]}
              />
            </Grid>
          </Grid>
        </Box>
      </Collapse>
    </Grid>
  );
  const page2 = (
    <Box>
      <Box textAlign="left">
        <Typography variant="body1" color="initial" gutterBottom>
          Click the add member (<PersonAddRounded size="small" />) button below
          to populate the members table.
        </Typography>
      </Box>
      <Box textAlign="left">
        <Typography variant="body1" color="initial" gutterBottom>
          An account must have only one member as Chairman and only one member
          as Secretary. An account can have as many officials and members. The
          total number of members required is ten (10).
        </Typography>
      </Box>

      <Box width="100%">
        <MembersTable membersList={membersList} deleteUser={deleteUser} />
      </Box>

      <Box
        display="flex"
        justifyContent="flex-end"
        position="relative"
        bottom={30}
      >
        <Fab onClick={handleOpen}>
          <PersonAddRounded />
        </Fab>
        <AddMember
          open={openAddMember}
          {...{
            setOpenAddMember,
          }}
        />
      </Box>
      <Dialog open={openDelete}>
        <DialogContent>
          <DialogContentText>
            Would you like to delete the information for {memberDelete}?
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button variant="text" color="secondary" onClick={handleDelete}>
            Delete
          </Button>
          <Button variant="text" color="secondary" onClick={handleDeleteCancel}>
            Cancel
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );

  const oldAccountFiles = (
    <Fragment>
      <Grid item>
        <FileButton
          id="certificate"
          color="secondary"
          text="Registration certificate"
          value={files.certificate && files.certificate.name}
          accept=".pdf"
          onChange={(e) => {
            setProfile({
              ...profile,
              files: { ...files, certificate: e.target.files[0] },
            });
          }}
        />
      </Grid>
      <Grid item>
        <FileButton
          id="activities"
          color="secondary"
          text="Annual Report of activities"
          value={files.activities && files.activities.name}
          accept=".pdf"
          onChange={(e) => {
            setProfile({
              ...profile,
              files: { ...files, activities: e.target.files[0] },
            });
          }}
        />
      </Grid>
      <Grid item>
        <FileButton
          id="profitandloss"
          color="secondary"
          text="Profit and loss statement"
          value={files.profitandloss && files.profitandloss.name}
          accept=".pdf"
          onChange={(e) => {
            setProfile({
              ...profile,
              files: { ...files, profitandloss: e.target.files[0] },
            });
          }}
        />
      </Grid>
      <Grid item>
        <FileButton
          id="balancesheet"
          color="secondary"
          text="Balance sheet"
          value={files.balancesheet && files.balancesheet.name}
          accept=".pdf"
          onChange={(e) => {
            setProfile({
              ...profile,
              files: { ...files, balancesheet: e.target.files[0] },
            });
          }}
        />
      </Grid>
    </Fragment>
  );
  const page3 = (
    <Grid container direction="column" alignItems="stretch" spacing={3}>
      <Grid item>
        <FileButton
          id="bylaws"
          color="secondary"
          text="Society bylaws"
          value={files.bylaws && files.bylaws.name}
          accept=".pdf"
          onChange={(e) => {
            setProfile({
              ...profile,
              files: { ...files, bylaws: e.target.files[0] },
            });
          }}
        />
      </Grid>
      {newAccount && (
        <Grid item>
          <FileButton
            id="feasibilityStudy"
            color="secondary"
            value={files.feasibilityStudy && files.feasibilityStudy.name}
            text="Feasibility Study/ Business Plan"
            accept=".pdf"
            onChange={(e) => {
              setProfile({
                ...profile,
                files: { ...files, feasibilityStudy: e.target.files[0] },
              });
            }}
          />
        </Grid>
      )}
      {!newAccount && oldAccountFiles}
    </Grid>
  );

  const prevPage = () => {
    setPage(page - 1);
  };

  const buttonClick = () => {
    switch (page) {
      case 1:
        if (page1Valid) {
          setPage(2);
        } else {
          setAlert("Please fill in all fields", "error");
        }
        break;
      case 2:
        const chairman = membersList.filter(({ role }) => role === "Chairman");
        const secretary = membersList.filter(
          ({ role }) => role === "Secretary"
        );
        if (membersList.length < 10) {
          const remaining = 10 - membersList.length;
          setAlert(
            `At least ${remaining} more ${
              remaining > 1 ? "members are" : "member is"
            } needed`,
            "error"
          );
        } else if (chairman.length !== 1) {
          setAlert("One member only must be a Chairman!", "warning");
        } else if (secretary.length !== 1) {
          setAlert("One member only must be a Secretary!", "warning");
        } else {
          setPage(3);
        }
        break;
      case 3:
        if (
          (newAccount && Object.keys(files).length < 2) ||
          (!newAccount && Object.keys(files).length < 5)
        ) {
          setAlert("Please upload all documents", "error");
        } else {
          uploadProfile();
        }
        break;
      default:
        break;
    }
  };

  return (
    <PageWrapper noLoad>
      <Box bgcolor="background.paper" py={8}>
        <Container maxWidth="sm">
          <Box>
            <Box my={1.5} textAlign="left">
              <Typography variant="h3" color="secondary" gutterBottom>
                Update Profile
              </Typography>
            </Box>
            <Collapse in={page === 1} collapsedHeight={0}>
              {page1}
            </Collapse>
            <Collapse in={page === 2} collapsedHeight={0}>
              {page2}
            </Collapse>
            <Collapse in={page === 3} collapsedHeight={0}>
              {page3}
            </Collapse>

            <Box my={3}>
              <Button
                variant="contained"
                color="primary"
                size="large"
                fullWidth
                onClick={buttonClick}
                disabled={loading}
              >
                {page === 3 ? "Submit" : "Next"}
              </Button>
            </Box>
            {page > 1 && (
              <Box my={3}>
                <Button
                  variant="contained"
                  color="secondary"
                  size="large"
                  fullWidth
                  onClick={prevPage}
                  disabled={loading}
                >
                  Back
                </Button>
              </Box>
            )}
          </Box>
        </Container>
      </Box>
    </PageWrapper>
  );
};

export default SocietyUpdate;
