import React, { Fragment, useContext, useState } from "react";
import {
  ErrorRounded,
  NotificationsRounded,
  CheckRounded,
  WarningRounded,
  InfoRounded,
} from "@material-ui/icons";
import {
  IconButton,
  Box,
  Badge,
  Popover,
  Link,
  ListItemAvatar,
  List,
  ListItemText,
  ListItem,
  Avatar,
  Divider,
  Typography,
  ListItemSecondaryAction,
  Tooltip,
} from "@material-ui/core";
import TimeAgo from "javascript-time-ago";
import en from "javascript-time-ago/locale/en";
import NotificationsContext from "../../context/notifications/notificationsContext";
import { Link as RouterLink, useHistory } from "react-router-dom";
import { makeStyles } from "@material-ui/core/styles";
import { green, cyan, red, orange } from "@material-ui/core/colors";

const useStyles = makeStyles((theme) => ({
  success: {
    backgroundColor: green[800],
  },
  warning: {
    backgroundColor: orange[800],
  },
  info: {
    backgroundColor: cyan[800],
  },
  error: {
    backgroundColor: red[800],
  },
  notSeen: {
    backgroundColor: theme.palette.background.paper,
  },
}));

const Bell = (props) => {
  const notificationsContext = useContext(NotificationsContext);
  const { newNotifications } = notificationsContext;

  const bell = (
    <Box color={props.color}>
      <NotificationsRounded />
    </Box>
  );
  if (newNotifications.length === 0) return bell;
  return (
    <Badge badgeContent={newNotifications.length} color="secondary">
      {bell}
    </Badge>
  );
};
export const Notice = (props) => {
  TimeAgo.addLocale(en);
  const timeAgo = new TimeAgo("en-US");
  const history = useHistory();
  const notificationsContext = useContext(NotificationsContext);
  const { readNotification } = notificationsContext;
  const {
    _id,
    title,
    category,
    details,
    dateCreated,
    link,
    seen,
    handleClose,
    Secondary,
    secondaryProps,
  } = props;
  const { success, warning, info, error, notSeen } = useStyles();
  const avatar = () => {
    switch (category) {
      case "success":
        return [<CheckRounded />, success];
      case "warning":
        return [<WarningRounded />, warning];
      case "info":
        return [<InfoRounded />, info];
      case "error":
        return [<ErrorRounded />, error];
      default:
        return [];
    }
  };
  const [avatarIcon, avatarColor] = avatar();
  const secondaryItem = (
    <ListItemSecondaryAction>
      <Secondary {...secondaryProps} />
    </ListItemSecondaryAction>
  );
  return (
    <ListItem
      alignItems="flex-start"
      onClick={() => {
        handleClose && handleClose();
        readNotification(_id);
        history.push(link);
      }}
      className={!seen && notSeen}
      button
    >
      <ListItemAvatar>
        <Avatar className={avatarColor}>{avatarIcon}</Avatar>
      </ListItemAvatar>
      <ListItemText
        primary={title}
        secondary={
          <Fragment>
            <Typography variant="caption" color="initial">
              {details}
            </Typography>
            - {timeAgo.format(new Date(dateCreated), en.narrow)}
          </Fragment>
        }
      />
      {Secondary && secondaryItem}
    </ListItem>
  );
};

const NotificationPopover = (props) => {
  const notificationsContext = useContext(NotificationsContext);
  const { newNotifications, notifications } = notificationsContext;
  const { open, id, handleClose, anchorEl } = props;
  const noNotification = <Box>No new notifications</Box>;
  const notices = (
    <List>
      {notifications.map((notice) => (
        <Fragment key={notice._id}>
          <Notice {...notice} handleClose={handleClose} />
          <Divider variant="inset" component="li" />
        </Fragment>
      ))}
    </List>
  );
  return (
    <Popover
      id={id}
      open={open}
      anchorEl={anchorEl}
      onClose={handleClose}
      anchorOrigin={{
        vertical: "bottom",
        horizontal: "center",
      }}
      transformOrigin={{
        vertical: "top",
        horizontal: "center",
      }}
    >
      <Box display="flex" flexDirection="column" height={400} width={300}>
        <Box>
          <Box display="flex" p={3} pb={0}>
            <Box
              fontSize={24}
              fontWeight="fontWeightBold"
              display="inline"
              flexGrow={1}
            >
              Notifications
            </Box>
            <Box py={1} alignSelf="flex-end" color="secondary.main">
              <Link
                color="secondary"
                component={RouterLink}
                to="/notifications"
                onClick={handleClose}
              >
                See all
              </Link>
            </Box>
          </Box>
        </Box>
        <Box flexGrow={1}>
          {newNotifications.length === 0 ? noNotification : notices}
        </Box>
      </Box>
    </Popover>
  );
};

const NotificationButton = (props) => {
  const [anchorEl, setAnchorEl] = useState(null);
  const history = useHistory();
  const { newNotifications } = useContext(NotificationsContext);
  const choice = (event) => {
    if (newNotifications.length && newNotifications.length > 0) {
      return handleClick(event);
    } else {
      history.push("/notifications");
    }
  };
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = (event) => {
    setAnchorEl(null);
  };

  const open = Boolean(anchorEl);
  const id = open ? "simple-popover" : undefined;

  return (
    <Fragment>
      <Tooltip title="Notifications">
        <IconButton aria-describedby={id} onClick={choice}>
          {props.children}
        </IconButton>
      </Tooltip>
      <NotificationPopover {...{ open, id, handleClose, anchorEl }} />
    </Fragment>
  );
};

const Notification = (props) => {
  return (
    <NotificationButton>
      <Bell color={props.color} />
    </NotificationButton>
  );
};

export default Notification;
