import React from "react";
import PageWrapper from "../components/layout/Wrapper/PageWrapper";
import {
  Box,
  Container,
  Typography,
  Button,
  Grid,
  Card,
  CardActions,
  CardContent,
  Divider,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import { grey } from "@material-ui/core/colors";

const useStyles = makeStyles((theme) => ({
  card: {
    //  `   height: 370,
  },
  media: {
    height: 200,
  },
  headerContainer: {
    paddingTop: theme.spacing(10),
    paddingBottom: theme.spacing(10),
  },
  headerBox: {
    backgroundColor: grey[100],
  },
  topHeading: {
    color: grey[900],
    [theme.breakpoints.down("sm")]: {
      fontSize: "3rem",
    },
  },
  subHeading: {
    color: grey[600],
    marginBottom: theme.spacing(3),
  },
  bodyBox: {
    paddingTop: theme.spacing(5),
    paddingBottom: theme.spacing(5),
  },
}));

const News = () => {
  const classes = useStyles();
  const news = {
    img:
      "https://images.unsplash.com/photo-1598833993682-fb5cf0d9c9d9?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=500&q=60",
    title: "Made in Nigeria",
    briefdesc:
      "The internet’s source of freely-usable images. Powered by creators everywhere. Search Search free high-resolution photos Trending: flower wall papers backgrounds happy love",
    date: Date.now(),
    longdesc:
      "Heat 1/2 cup of the broth in a pot until simmering, add saffron and set aside for 10 minutes. Heat oil in a (14- to 16-inch) paella pan or a large, deep skillet over medium-high heat. Add chicken, shrimp and chorizo, and cook, stirring occasionally until lightly browned, 6 to 8 minutes. Transfer shrimp to a large plate and set aside, leaving chicken and chorizo in the pan. Add pimentón, bay leaves, garlic, tomatoes, onion, salt and pepper, and cook, stirring often until thickened and fragrant, about 10 minutes. Add saffron broth and remaining 4 1/2 cups chicken broth; bring to a boil. Add rice and stir very gently to distribute. Top with artichokes and peppers, and cook without stirring, until most of the liquid is absorbed, 15 to 18 minutes. Reduce heat to medium-low, add reserved shrimp and mussels, tucking them down into the rice, and cook again without stirring, until mussels have opened and rice is just tender, 5 to 7 minutes more. (Discard any mussels that don’t open.) Set aside off of the heat to let rest for 10 minutes, and then serve.",
  };
  const NewsComponent = (index, news) => {
    return (
      <Grid key={index} item md={4} xs={12}>
        <Card className={classes.card} elevation={2}>
          {/* <CardMedia
            className={classes.media}
            image={news.img}
            title="Contemplative Reptile"
          /> */}
          <CardContent>
            <Box pt={2}>
              <Typography gutterBottom variant="h5" component="h2">
                {news.title}
              </Typography>
              <Typography variant="body2" color="textSecondary" component="p">
                {news.briefdesc}
              </Typography>
            </Box>
          </CardContent>
          <CardActions>
            <Button size="small" color="primary">
              Learn More
            </Button>
          </CardActions>
        </Card>
      </Grid>
    );
  };
  const newsCards = [];
  for (let index = 0; index < 20; index++) {
    newsCards.push(NewsComponent(index, news));
  }

  return (
    <PageWrapper>
      <Box className={classes.headerBox}>
        <Container className={classes.headerContainer} maxWidth="md">
          <Typography
            className={classes.topHeading}
            variant="h1"
            color="initial"
          >
            News
          </Typography>
          <Typography
            className={classes.subHeading}
            variant="subtitle1"
            color="initial"
          >
            Stay up to date with the latest happenings in the national Co
            operative world...
          </Typography>
        </Container>
      </Box>
      <Divider />
      <Box className={classes.bodyBox}>
        <Container maxWidth="md">
          <Grid container spacing={3}>
            {newsCards}
          </Grid>
        </Container>
      </Box>
    </PageWrapper>
  );
};

export default News;
