import React, { useState, useContext } from "react";
import {
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  Box,
  IconButton,
  TextField,
  DialogActions,
} from "@material-ui/core";
import { AttachFileRounded } from "@material-ui/icons";
import fileContext from "../components/context/files/fileContext";
import Axios from "axios";

const UploadResources = ({ type }) => {
  const [open, setOpen] = useState(false);
  return (
    <React.Fragment>
      <Button onClick={() => setOpen(!open)} variant="outlined">
        {`Upload ${type || "Resource"}`}
      </Button>
      <UploadWindow
        type={type}
        open={open}
        handleClose={() => setOpen(false)}
      />
    </React.Fragment>
  );
};

const UploadWindow = (props) => {
  const { open, handleClose, type } = props;
  const [title, setTitle] = useState("");
  const [description, setDescription] = useState("");
  const [file, setFile] = useState(null);
  const { getResources } = useContext(fileContext);
  const _onSubmit = async () => {
    const formData = new FormData();
    let url;
    const cloud = process.env.REACT_APP_CLOUD;
    formData.append("file", file);
    formData.append("upload_preset", process.env.REACT_APP_UPLOAD_PRESET);
    formData.append("cloud_name", cloud);
    formData.append("folder", process.env.REACT_APP_FOLDER);
    const upload_url = `https://api.cloudinary.com/v1_1/${cloud}/auto/upload`;
    try {
      await fetch(upload_url, {
        method: "POST",
        body: formData,
      })
        .then((res) => res.json())
        .then(async (response) => {
          url = response.secure_url;
        });
      await Axios.post("/api/resources", {
        title,
        details: description,
        url,
        type,
      });
      getResources();
      setTitle("");
      setDescription("");
      setFile(null);
    } catch (err) {
      console.log(err);
    }
  };

  return (
    <Dialog open={open} onClose={handleClose}>
      <DialogTitle>Upload Resource</DialogTitle>
      <DialogContent>
        <Box my={2}>
          <TextField
            variant="outlined"
            label="Title of Resource"
            fullWidth
            value={title}
            onChange={(val) => setTitle(val.target.value)}
          />
        </Box>
        <Box my={2}>
          <TextField
            variant="outlined"
            label="Description"
            fullWidth
            value={description}
            onChange={(val) => setDescription(val.target.value)}
          />
        </Box>
        <Box flexDirection="row" display="flex" alignItems="center" my={1}>
          <label style={{ flexGrow: 1 }}>
            {file ? file.name : "No file choosen"}
          </label>
          <Box>
            <Box display="none">
              <input
                accept=".pdf,.doc"
                id="icon-button-file"
                type="file"
                onChange={(val) => {
                  setFile(val.target.files[0]);
                }}
              />
            </Box>
            <label htmlFor="icon-button-file">
              <IconButton
                color="primary"
                aria-label="upload picture"
                component="span"
              >
                <AttachFileRounded />
              </IconButton>
            </label>
          </Box>
        </Box>
        <Box width={500} />
      </DialogContent>
      <DialogActions>
        <Button
          variant="text"
          disabled={!title || !description || !file}
          onClick={() => {
            _onSubmit();
            handleClose();
          }}
        >
          Upload{" "}
        </Button>
        <Button variant="text" onClick={handleClose}>
          Cancel
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default UploadResources;
