import React, { useContext } from "react";
import PageWrapper from "../../components/layout/Wrapper/PageWrapper";

import Chart from "../../resources/Charts.gif";
import User from "../../resources/Add User.gif";
import File from "../../resources/File bundle.gif";
import News from "../../resources/News.gif";
import Requests from "../../resources/Customer Survey-amico.png";
import Library from "../../resources/Library.gif";

import AuthContext from "../../components/context/auth/authContext";
import Dashboard from "../../components/routing/Dashboard";
import { Redirect, useHistory } from "react-router-dom";
import {
  Box,
  Container,
  Typography,
  Grid,
  Card,
  CardContent,
  CardMedia,
  CardActionArea,
  IconButton,
} from "@material-ui/core";
import { SettingsPowerRounded } from "@material-ui/icons";

const role = "admin";

const Admin = (props) => {
  const authContext = useContext(AuthContext);
  const page = Dashboard();
  const history = useHistory();

  if (authContext.userRole && authContext.userRole !== role)
    return <Redirect to={page} />;

  const cards = [
    // {
    //   title: "Analytics",
    //   image: Chart,
    //   link: "/admin/analytics",
    //   description: "",
    // },
    {
      title: "Users",
      image: User,
      link: "/admin/users",
      description: "",
    },
    {
      title: "Manage Resources",
      image: File,
      link: "/admin/resources",
      description: "",
    },
    // { title: "News Update", image: News, link: "", description: "" },
    {
      title: "Requests",
      image: Requests,
      link: "/admin/requests",
      description: "",
    },
    {
      title: "E-Learning",
      image: Library,
      link: "/admin/e-learning",
      description: "",
    },
  ];
  return (
    <PageWrapper>
      <Box py={5}>
        <Container maxWidth="md">
          <Box py={5} display="flex" direction="row">
            <Box flexGrow={1}>
              <Typography variant="h3" color="initial" gutterBottom>
                Welcome Administrator
              </Typography>
              <Typography variant="subtitle1" color="initial">
                What would you like to do today?
              </Typography>
            </Box>
            <Box>
              <IconButton aria-label="logout" onClick={authContext.logout}>
                <SettingsPowerRounded />
              </IconButton>
            </Box>
          </Box>
          <Box>
            <Grid container spacing={5}>
              {cards.map(({ title, image, link, description }, index) => (
                <Grid item key={index} xs={12} md={4}>
                  <Card>
                    <CardActionArea onClick={() => history.push(link)}>
                      <CardMedia
                        component="img"
                        image={image}
                        height="250"
                        alt={title}
                      />
                      <CardContent>
                        <Typography variant="subtitle1" color="initial">
                          {title}
                        </Typography>
                      </CardContent>
                    </CardActionArea>
                  </Card>
                </Grid>
              ))}
            </Grid>
          </Box>
        </Container>
      </Box>
    </PageWrapper>
  );
};

export default Admin;
