import React, { useContext, useEffect } from "react";
import {
  Box,
  Container,
  makeStyles,
  Typography,
  IconButton,
} from "@material-ui/core";
import PageWrapper from "../components/layout/Wrapper/PageWrapper";
import { CloudDownloadRounded, ArrowBackRounded } from "@material-ui/icons";
import fileContext from "../components/context/files/fileContext";
import { DataGrid } from "@material-ui/data-grid";

const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
  container: {
    minHeight: 300,
  },
});

const Elearning = () => {
  const { getResources, resources } = useContext(fileContext);

  const [display, setDisplay] = React.useState([]);

  useEffect(() => {
    getResources();
  }, []);

  useEffect(() => {
    Array.isArray(resources) &&
      setDisplay(resources.filter((file) => file.type === "E-learning"));
  }, [resources]);

  return (
    <PageWrapper>
      <Box py={5}>
        <Container maxWidth="md">
          <Typography variant="h2" color="initial" gutterBottom>
            E-Learning
          </Typography>
          <Box height={400}>
            <DataGrid
              rows={display.map((resource, index) => {
                return {
                  ...resource,
                  id: index,
                };
              })}
              columns={[
                { field: "title", headerName: "Title", width: 250 },
                { field: "details", headerName: "Description", width: 400 },
                {
                  field: "dateUploaded",
                  headerName: "Date Uploaded",
                  width: 150,
                  valueGetter: (params) =>
                    new Date(params.value).toLocaleDateString(),
                },
                {
                  field: "url",
                  headerName: "Download",
                  width: 100,
                  renderCell: (params) => (
                    <IconButton
                      component="a"
                      color="primary"
                      href={params.value}
                      target="_blank"
                    >
                      <CloudDownloadRounded />
                    </IconButton>
                  ),
                },
              ]}
            />
          </Box>
        </Container>
      </Box>
    </PageWrapper>
  );
};

export default Elearning;
