import React, { useState, useContext, useEffect } from "react";
import { Link as RouterLink } from "react-router-dom";
import {
  TextField,
  Button,
  Typography,
  Grid,
  Box,
  Container,
  CircularProgress,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import PageWrapper from "../components/layout/Wrapper/PageWrapper";
import PasswordObj from "../components/layout/FormItems";

import AlertContext from "../components/context/alert/alertContext";
import AuthContext from "../components/context/auth/authContext";

import Dashboard from "../components/routing/Dashboard";

const useStyles = makeStyles((theme) => ({
  root: { paddingTop: theme.spacing(10), paddingBottom: theme.spacing(10) },
  margin: {
    margin: theme.spacing(1),
  },
  links: {
    textDecoration: "none",
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(2),
  },
  textField: {
    width: "-webkit-fill-available",
  },
}));

const Login = (props) => {
  const classes = useStyles();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const alertContext = useContext(AlertContext);
  const authContext = useContext(AuthContext);
  const { setAlert } = alertContext;
  const {
    isAuthenticated,
    loading,
    error,
    login,
    path,
    clearPath,
    userRole,
    getPath,
  } = authContext;

  const loginClick = () => {
    if (email === "" || password === "") {
      setAlert("Please fill in all fields", "error");
    } else if (password.length < 8) {
      setAlert(
        `Minimum password length is 8, you gave ${
          password.length.toFixed() +
          " " +
          (password.length > 1 ? "characters" : "character")
        } `,
        "warning"
      );
    } else {
      login({ email, password });
    }
  };

  const page = Dashboard();
  useEffect(() => {
    if (error) {
      setAlert(error, "error");
    } else if (isAuthenticated && !loading) {
      props.history.push(getPath() || page);
    }
  }, [isAuthenticated, loading, props.history, error]);

  return (
    <PageWrapper>
      <Box bgcolor="background.paper" py={12}>
        <Container maxWidth="sm">
          <Grid
            container
            justify="center"
            spacing={3}
            direction="column"
            alignItems="stretch"
          >
            <Grid container justify="center">
              <Typography variant="h3" color="secondary" gutterBottom>
                Sign in
              </Typography>
            </Grid>

            <Grid item>
              <TextField
                id="email"
                className={classes.textField}
                label="Email"
                value={email}
                onChange={(e) => {
                  setEmail(e.target.value);
                }}
                variant="outlined"
                required
              />
            </Grid>
            <Grid item>
              <PasswordObj
                id="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                fullWidth
                required
              />
            </Grid>

            <Grid item>
              <Button
                variant="contained"
                color="primary"
                size="large"
                fullWidth
                onClick={loginClick}
                disabled={loading}
              >
                {loading ? (
                  <CircularProgress size={25} color="inherit" />
                ) : (
                  "Login"
                )}
              </Button>
            </Grid>
            <Grid container direction="row" justify="space-between">
              <Grid item>
                <Typography
                  className={classes.links}
                  variant="caption"
                  color="primary"
                  component={RouterLink}
                  to="/forgotpassword"
                >
                  Forgot Password?
                </Typography>
              </Grid>
              <Grid item>
                <Typography
                  className={classes.links}
                  variant="caption"
                  color="primary"
                  component={RouterLink}
                  to="/register"
                >
                  Create a new account
                </Typography>
              </Grid>
            </Grid>
          </Grid>
        </Container>
      </Box>
    </PageWrapper>
  );
};

export default Login;
