import React from "react";
import {
  Box,
  Container,
  Grid,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Icon,
  Typography,
} from "@material-ui/core";
import { grey } from "@material-ui/core/colors";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: grey["A400"],
    paddingTop: theme.spacing(7),
    paddingBottom: theme.spacing(7),
    "& span": {
      color: grey[500],
      fontSize: "100%",
    },
  },
  base: {
    backgroundColor: grey[900],
    padding: theme.spacing(1),
    textAlign: "center",
    "& span": {
      color: grey[500],
      fontSize: "100%",
      marginRight: theme.spacing(1),
    },
  },
}));

const Footer = () => {
  const classes = useStyles();
  const date = new Date();
  return (
    <>
      <Box className={classes.root}>
        <Container maxWidth="md">
          <Grid container spacing={5}>
            <Grid item xs={12} md={4}>
              <Box color={grey[500]}>
                <Typography variant="h6" color="initial" gutterBottom>
                  Federal Ministry of Labour and Employment
                </Typography>
                <Typography
                  variant="body2"
                  component="p"
                  color="initial"
                  gutterBottom
                >
                  Cooperative Development and Social Security Management
                  Information System
                </Typography>
              </Box>
            </Grid>
            <Grid item xs={12} md={4}>
              <Box color={grey[500]}>
                <Typography variant="h6" color="initial" gutterBottom>
                  Follow Us
                </Typography>
              </Box>
              <List>
                <ListItem button>
                  <ListItemIcon>
                    <Icon className="fab fa-facebook-f" />
                  </ListItemIcon>
                  <ListItemText primary="Facebook" />
                </ListItem>
                <ListItem button>
                  <ListItemIcon>
                    <Icon className="fab fa-twitter" />
                  </ListItemIcon>
                  <ListItemText primary="Twitter" />
                </ListItem>
                <ListItem>
                  <ListItemIcon>
                    <Icon className="fab fa-linkedin-in" />
                  </ListItemIcon>
                  <ListItemText primary="Linked In" />
                </ListItem>
              </List>
            </Grid>
            <Grid item xs={12} md={4}>
              <Box color={grey[500]}>
                <Typography variant="h6" color="initial" gutterBottom>
                  Contact Us
                </Typography>
                <Typography
                  variant="body2"
                  component="p"
                  color="initial"
                  gutterBottom
                >
                  Federal Ministry of Labour and Employment
                </Typography>
                <Typography
                  variant="body2"
                  component="p"
                  color="initial"
                  gutterBottom
                >
                  P.M.B. 04, Federal Secretariat Complex Phase I, Annex II,
                  Shehu Shagari Way, Abuja FCT. Nigeria
                </Typography>
              </Box>
            </Grid>
          </Grid>
        </Container>
      </Box>
      <Box className={classes.base}>
        <Typography variant="caption" color="initial">
          <Icon className="far fa-copyright" />
          Copyright {date.getFullYear()} - All rights reserved
        </Typography>
      </Box>
    </>
  );
};

export default Footer;
