import React from "react";
import PageWrapper from "../../components/layout/Wrapper/PageWrapper";
import {
  Box,
  Container,
  Typography,
  IconButton,
  Dialog,
  DialogContentText,
  Button,
  DialogTitle,
  DialogContent,
  DialogActions,
} from "@material-ui/core";
import { Link } from "react-router-dom";
import { ArrowBackRounded } from "@material-ui/icons";

import { makeStyles, withStyles } from "@material-ui/core/styles";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import { DataGrid } from "@material-ui/data-grid";
import Axios from "axios";
import AlertContext from "../../components/context/alert/alertContext";

const AntTabs = withStyles((theme) => ({
  root: {
    borderBottom: "1px solid #e8e8e8",
  },
  indicator: {
    backgroundColor: theme.palette.primary.main,
  },
}))(Tabs);

const AntTab = withStyles((theme) => ({
  root: {
    textTransform: "none",
    minWidth: 72,
    fontWeight: theme.typography.fontWeightRegular,
    marginRight: theme.spacing(4),
    fontFamily: [
      "-apple-system",
      "BlinkMacSystemFont",
      '"Segoe UI"',
      "Roboto",
      '"Helvetica Neue"',
      "Arial",
      "sans-serif",
      '"Apple Color Emoji"',
      '"Segoe UI Emoji"',
      '"Segoe UI Symbol"',
    ].join(","),
    "&:hover": {
      color: theme.palette.primary.light,
      opacity: 1,
    },
    "&$selected": {
      color: theme.palette.primary.dark,
      fontWeight: theme.typography.fontWeightMedium,
    },
    "&:focus": {
      color: theme.palette.primary.light,
    },
  },
  selected: {},
}))((props) => <Tab disableRipple {...props} />);

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  padding: {
    padding: theme.spacing(3),
  },
  demo1: {
    backgroundColor: theme.palette.background.default,
  },
  demo2: {
    backgroundColor: "#2e1534",
  },
}));

const Users = () => {
  const classes = useStyles();
  const [id, setId] = React.useState(null);
  const [open, setOpen] = React.useState(false);
  const [value, setValue] = React.useState(0);
  const [users, setUsers] = React.useState([]);
  const [rows, setRows] = React.useState([]);
  const [loading, setLoading] = React.useState(true);
  const { setAlert } = React.useContext(AlertContext);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const deleteUser = async (_id) => {
    try {
      await Axios.delete(`/api/users/${id}`);
      setUsers(users.filter((user) => user._id !== _id));
    } catch (err) {
      console.log(err);
      setAlert("Oops! We couldn't delete the user", "error");
    }
    setId(null);
  };

  React.useEffect(() => {
    if (id) {
      setOpen(true);
    } else {
      setOpen(false);
    }
    return () => {
      setId(null);
    };
  }, [id]);

  const getUsers = async () => {
    setLoading(true);
    try {
      const res = await Axios.get("/api/users/admin");
      setUsers(res.data);
    } catch (err) {
      console.log(err);
    }
    setLoading(false);
  };

  React.useEffect(() => {
    getUsers();
  }, []);

  React.useEffect(() => {
    switch (value) {
      case 0:
        setRows(users.filter((req) => req.role === "society"));
        break;
      case 1:
        setRows(users.filter((req) => req.role === "extauditor"));
        break;
      case 2:
        setRows(users.filter((req) => req.role === "intauditor"));
        break;
      case 3:
        setRows(users.filter((req) => req.role === "financial"));
        break;
      case 4:
        setRows(users.filter((req) => req.role === "legal"));
        break;
      case 5:
        setRows(users.filter((req) => req.role === "consultant"));
        break;
    }
  }, [users, value]);
  return (
    <PageWrapper>
      <Box py={5}>
        <Container maxWidth="md">
          <Box display="flex" flexDirection="row">
            <IconButton
              style={{ marginLeft: 20, height: 50, width: 50 }}
              component={Link}
              to="/admin"
            >
              <ArrowBackRounded />
            </IconButton>
            <Typography variant="h3" color="initial" gutterBottom>
              Users
            </Typography>
          </Box>
          <div className={classes.demo1}>
            <AntTabs
              value={value}
              onChange={handleChange}
              aria-label="ant example"
              scrollButtons="auto"
            >
              <AntTab label="Societies" />
              <AntTab label="General Auditors" />
              <AntTab label="Government Auditors" />
              <AntTab label="Financial Institutions" />
              <AntTab label="Legal Institutions" />
              <AntTab label="Consultants" />
            </AntTabs>
            <Typography className={classes.padding} />
          </div>
          <Box height={400}>
            <DataGrid
              loading={loading}
              rows={rows.map((row) => {
                return {
                  ...row,
                  id: row._id,
                };
              })}
              columns={[
                { field: "name", headerName: "Name", width: 300 },
                { field: "email", headerName: "Email", width: 300 },
                {
                  field: "dateCreated",
                  headerName: "Date",
                  width: 200,
                  valueGetter: (params) =>
                    new Date(params.value).toLocaleDateString(),
                },
              ]}
              onRowSelected={(row) => setId(row.data.id)}
            />
          </Box>
        </Container>
      </Box>
      <Dialog open={open} onClose={() => setOpen(false)}>
        <DialogTitle>Delete user?</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Remove the data of this user from the database.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => deleteUser(id)} color="primary">
            Yes
          </Button>
          <Button onClick={() => setOpen(false)} color="default">
            No
          </Button>
        </DialogActions>
      </Dialog>
    </PageWrapper>
  );
};

export default Users;
