import React, { useContext } from "react";
import AuthContext from "../context/auth/authContext";
import { Redirect } from "react-router-dom";

const Dashboard = () => {
  const authContext = useContext(AuthContext);
  const { userRole } = authContext;
  switch (userRole) {
    case "society":
      return "/society";
    case "intauditor":
    case "extauditor":
      return "/auditor";
    case "consultant":
      return "/consultant";
    case "legal":
      return "/legal";
    case "financial":
      return "/financial";
    case "admin":
      return "/admin";
    default:
      return "/society";
  }
};

export default Dashboard;
