import React, { useContext, useState, Fragment } from "react";
import { Notice } from "../Wrapper/Notification";
import { List, Divider } from "@material-ui/core";
import NotificationContext from "../../context/notifications/notificationsContext";
import Pagination from "@material-ui/lab/Pagination";

const Notifications = (props) => {
  const { notifications } = useContext(NotificationContext);
  const perPage = 20;
  const [page, setPage] = useState(1);
  const count = Math.floor(notifications.length / perPage) + 1;
  const shownNotifications = notifications.slice(
    (page - 1) * perPage,
    page * perPage
  );

  const handleChange = (event, value) => {
    setPage(value);
  };
  return (
    <Fragment>
      <List>
        {shownNotifications.map((notice) => (
          <Fragment key={notice._id}>
            <Notice {...notice} />
            <Divider variant="inset" component="li" />
          </Fragment>
        ))}
      </List>
      <Pagination
        count={count}
        onChange={handleChange}
        showFirstButton={page !== 1}
        showLastButton={page !== count}
      />
    </Fragment>
  );
};

export default Notifications;
