/* eslint-disable no-use-before-define */
import React from "react";
import TextField from "@material-ui/core/TextField";
import Autocomplete, {
  createFilterOptions,
} from "@material-ui/lab/Autocomplete";

const filter = createFilterOptions();

export default function SelectProp(props) {
  const [value, setValue] = React.useState(null);

  const { onSelect } = props;

  return (
    <Autocomplete
      value={value}
      onChange={(event, newValue) => {
        if (typeof newValue === "string") {
          setValue({
            title: newValue,
          });
          onSelect && onSelect({ Value: newValue, Label: newValue });
        } else if (newValue && newValue.inputValue) {
          // Create a new value from the user input
          setValue({
            title: newValue.inputValue,
          });
          onSelect &&
            newValue.value &&
            onSelect({ Value: newValue.value, Label: newValue.label });
        } else {
          setValue(newValue);
          onSelect && onSelect({ Value: newValue, Label: newValue });
        }
      }}
      filterOptions={(options, params) => {
        const filtered = filter(options, params);
        // Suggest the creation of a new value
        if (params.inputValue !== "") {
          filtered.push({
            value: params.inputValue,
            inputValue: params.inputValue,
            label: params.inputValue,
            title: `Add "${params.inputValue}"`,
          });
        }
        return filtered;
      }}
      selectOnFocus
      clearOnBlur
      handleHomeEndKeys
      id="property-search"
      options={Object.keys(values)
        .map((key) => {
          return {
            title: values[key],
            label: values[key],
            inputValue: values[key],
            value: key,
          };
        })
        .sort((a, b) => a.title - b.title)}
      getOptionLabel={(option) => {
        // Value selected with enter, right from the input
        if (typeof option === "string") {
          return option;
        }
        // Add "xxx" option created dynamically
        if (option.inputValue) {
          return option.inputValue;
        }
        // Regular option
        return option.title;
      }}
      renderOption={(option) => option.title}
      style={{ width: "100%" }}
      freeSolo
      renderInput={(params) => (
        <TextField {...params} label="Property" variant="outlined" />
      )}
    />
  );
}

// Property values
const values = {
  name: "Name",
  newAccount: "New Account",
  regno: "Registration Number",
  industry: "Industry",
  address: "Address",
  roadStreet: "Road/Street",
  lga: "L.G.A.",
  state: "State",
  lastAudit: "Last Audit",
  lastElection: "Last Election",
  lastAuditYear: "Last Audit Year",
  auditClass: "Audit Class",
  paidUpShareCapital: "Paid Share Per Capital",
  membersList: "Members List",
  certificate: "Registration Certificate",
  activities: "Annual Report of Activities",
  profitandloss: "Profit And Loss",
  balancesheet: "Balance Sheet",
  bylaws: "Bylaws",
};
