import React from "react";
import PageWrapper from "../../components/layout/Wrapper/PageWrapper";
import {
  Box,
  Container,
  Typography,
  IconButton,
  TextField,
  Button,
  Divider,
  CircularProgress,
  Grid,
  Paper,
  Chip,
  Menu,
  MenuItem,
} from "@material-ui/core";
import { Link, useParams, Redirect } from "react-router-dom";
import { ArrowBackRounded } from "@material-ui/icons";

import { makeStyles, withStyles } from "@material-ui/core/styles";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import { DataGrid } from "@material-ui/data-grid";
import Axios from "axios";
import { Comment, Form } from "../Requests";
import Dashboard from "../../components/routing/Dashboard";
import AuthContext from "../../components/context/auth/authContext";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  padding: {
    padding: theme.spacing(1),
  },
  demo1: {
    backgroundColor: theme.palette.background.default,
  },
  demo2: {
    backgroundColor: "#2e1534",
  },
}));

const role = ["admin", "superAdmin"];

const Requests = () => {
  const page = Dashboard();
  const authContext = React.useContext(AuthContext);
  const classes = useStyles();
  const [request, setRequest] = React.useState([]);
  const [status, setStatus] = React.useState(null);
  const [loading, setLoading] = React.useState(true);
  const [title, setTitle] = React.useState("");
  const [description, setDescription] = React.useState("");
  const [dateCreated, setDateCreated] = React.useState(null);
  const [commentText, setCommentText] = React.useState("");
  const [sendingComment, setSendingComment] = React.useState(false);
  const [comments, setComments] = React.useState([]);

  const [sender, setSender] = React.useState(null);

  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleOpen = (event) => {
    status === "Pending" && setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const { id } = useParams();

  const getRequest = async () => {
    setLoading(true);
    try {
      const res = await Axios.get(`/api/requests/${id}`);
      setRequest(res.data.request.content);
      setTitle(res.data.request.title);
      setDescription(res.data.request.description);
      setComments(res.data.comments);
      setDateCreated(res.data.request.dateCreated);
      setStatus(res.data.request.status);
      setSender(res.data.request.senderId.name);
    } catch (err) {
      console.log(err);
    }
    setLoading(false);
  };

  React.useEffect(() => {
    getRequest();
  }, []);

  const sendComment = async () => {
    setSendingComment(true);
    try {
      const res = await Axios.post(`/api/requests/comment/${id}`, {
        text: commentText,
      });
      setComments(res.data.comments);
      setCommentText("");
    } catch (err) {
      console.log(err);
    }
    setSendingComment(false);
  };

  const cancelRequest = async () => {
    try {
      await Axios.post("/api/requests/cancel", { id });
      setStatus("Cancelled");
    } catch (err) {
      console.log(err);
    }
  };

  const approveRequest = async () => {
    try {
      await Axios.post("/api/requests/approve", { id });
      setStatus("Approved");
    } catch (err) {
      console.log(err);
    }
  };

  React.useEffect(() => {
    getRequest();
  }, []);

  if (authContext.userRole && !role.includes(authContext.userRole))
    return <Redirect to={page} />;
  if (loading)
    return (
      <PageWrapper>
        <Box
          height={600}
          display="flex"
          alignItems="center"
          justifyContent="center"
        >
          <CircularProgress />
          <Typography variant="subtitle1" color="initial">
            Loading...
          </Typography>
        </Box>
      </PageWrapper>
    );

  return (
    <PageWrapper>
      <Box py={5}>
        <Container maxWidth="lg">
          <Grid container>
            <Grid item xs={12} md={8}>
              <Box p={2}>
                <Box display="flex" alignItems="baseline" color="gray">
                  <Typography variant="h4" color="textPrimary" gutterBottom>
                    {title}
                  </Typography>
                  <Box px={1} />
                  {sender && (
                    <Typography variant="body2" color="initial">
                      {`by ${sender}`}
                    </Typography>
                  )}
                </Box>
                <Box display="flex" alignItems="baseline" color="gray">
                  <Typography
                    variant="subtitle1"
                    color="textPrimary"
                    gutterBottom
                  >
                    {description}
                  </Typography>
                  <Box px={0.5} />
                  {dateCreated && (
                    <Typography variant="body2" color="inherit">
                      - {new Date(dateCreated).toDateString()}
                    </Typography>
                  )}
                  <Box px={1} />
                  {status && (
                    <>
                      <Chip
                        label={status}
                        color="secondary"
                        size="small"
                        onClick={handleOpen}
                      />
                      <Menu
                        id="status-change"
                        anchorEl={anchorEl}
                        keepMounted
                        open={Boolean(anchorEl)}
                        onClose={handleClose}
                      >
                        <MenuItem
                          onClick={() => {
                            handleClose();
                            approveRequest();
                          }}
                        >
                          Approve
                        </MenuItem>
                        <MenuItem
                          onClick={() => {
                            handleClose();
                            cancelRequest();
                          }}
                        >
                          Cancel
                        </MenuItem>
                      </Menu>
                    </>
                  )}
                </Box>
                <Form content={request} />
              </Box>
            </Grid>
            <Grid item xs={12} md={4}>
              <Paper>
                <Box p={5} pb={0}>
                  <TextField
                    label="Comment"
                    variant="outlined"
                    fullWidth
                    value={commentText}
                    onChange={(e) => setCommentText(e.target.value)}
                  />
                  <Box pt={1} display="flex" justifyContent="flex-end">
                    <Button
                      variant="contained"
                      color="primary"
                      disabled={sendingComment}
                      onClick={sendComment}
                    >
                      Send
                    </Button>
                  </Box>
                </Box>
                <Box py={3}>
                  <Divider />
                </Box>
                <Comment comments={comments} />
              </Paper>
            </Grid>
          </Grid>
        </Container>
      </Box>
    </PageWrapper>
  );
};

export default Requests;
