import React from "react";
import Autocomplete from "@material-ui/lab/Autocomplete";
import TextField from "@material-ui/core/TextField";
import CircularProgress from "@material-ui/core/CircularProgress";
import axios from "axios";
import { ContactSupportOutlined } from "@material-ui/icons";
import {
  Typography,
  ListItem,
  ListItemText,
  ListItemSecondaryAction,
  MenuItem,
} from "@material-ui/core";

const SearchUsers = ({ role, label, onSelect }) => {
  const [open, setOpen] = React.useState(false);
  const [loading, setLoading] = React.useState(false);
  const [options, setOptions] = React.useState([]);
  const [inputText, setInputText] = React.useState("");
  const [value, setValue] = React.useState(null);

  React.useEffect(() => {
    Search();
  }, []);

  React.useEffect(() => {
    onSelect && value && onSelect(value);
  }, [value, onSelect]);

  const Search = async () => {
    setLoading(true);
    try {
      const res = await axios.get(`/api/users/search/${role || "society"}`);
      console.log(res.data);
      setOptions(res.data);
    } catch (err) {
      console.log(err);
    }
    setLoading(false);
  };

  return (
    <Autocomplete
      id="asynchronous-demo"
      style={{ width: 300 }}
      open={open}
      value={value}
      onOpen={() => {
        setOpen(true);
      }}
      onClose={() => {
        setOpen(false);
      }}
      getOptionSelected={(option, value) =>
        option.name === value.name && option.userId === value.userId
      }
      getOptionLabel={(option) => option.name}
      options={options}
      onChange={(event, newValue) => {
        newValue && setValue(newValue);
      }}
      loading={loading}
      inputValue={inputText}
      onInputChange={(event, newInputValue) => {
        setInputText(newInputValue);
      }}
      renderOption={(option, { inputValue }) => (
        <ListItem>
          <ListItemText
            primary={
              <div>
                <Typography variant="h6" color="initial" gutterBottom>
                  {option.name}
                </Typography>
                <Typography variant="body2" color="initial" gutterBottom>
                  {option.userId && option.userId.email.toLowerCase()}
                </Typography>
                <Typography variant="body2" color="initial" gutterBottom>
                  {option.contactPhone}
                </Typography>
              </div>
            }
          />
          <Typography variant="caption" color="primary" gutterBottom>
            {option.state}
          </Typography>
        </ListItem>
      )}
      renderInput={(params) => (
        <TextField
          {...params}
          label={label || "Search"}
          variant="outlined"
          InputProps={{
            ...params.InputProps,
            endAdornment: (
              <React.Fragment>
                {loading ? (
                  <CircularProgress color="inherit" size={20} />
                ) : null}
                {params.InputProps.endAdornment}
              </React.Fragment>
            ),
          }}
        />
      )}
    />
  );
};

export default SearchUsers;
