import React, { useState, Fragment, useContext } from "react";
import {
  Collapse,
  List,
  Toolbar,
  Typography,
  Divider,
  ListItem,
  ListItemIcon,
  ListItemText,
  Badge,
  Box,
} from "@material-ui/core";

import {
  PersonRounded,
  PeopleRounded,
  InsertDriveFileRounded,
  AssignmentRounded,
  RoomServiceRounded,
  PowerSettingsNewRounded,
  ExpandLessRounded,
  ExpandMoreRounded,
} from "@material-ui/icons";
import Image from "material-ui-image";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import { Link as RouterLink } from "react-router-dom";

import AuthContext from "../../context/auth/authContext";

const useStyles = makeStyles((theme) => ({
  title: { color: "white", margin: theme.spacing(2), textDecoration: "none" },
  drawerItems: {
    color: "white",
    "& svg": {
      color: "#fafafa",
    },
  },
  nested: {
    paddingLeft: theme.spacing(4),
  },
}));

const SocietyDrawer = (props) => {
  // eslint-disable-next-line
  const authContext = useContext(AuthContext);
  const { handleDrawerToggle } = props;

  const classes = useStyles();

  const [isOpen, setIsOpen] = useState(props.selectedIndex || -1);
  const [selectedIndex, setSelectedIndex] = useState(props.selectedIndex || -1);
  const [selectedSubIndex, setSelectedSubIndex] = useState(
    props.selectedSubIndex || -1
  );

  const drawerMenu = [
    {
      text: "Profile",
      to: "/society",
      type: "link",
      icon: <PersonRounded />,
    },
    {
      text: "Members",
      to: "/society/members",
      type: "link",
      icon: <PeopleRounded />,
    },
    {
      text: "Mandatory Returns",
      to: "/society/mandatory_returns",
      type: "link",
      icon: <InsertDriveFileRounded />,
    },
    { type: "divider" },
    {
      text: "Requests",
      to: "/society/requests",
      type: "link",
      icon: <AssignmentRounded />,
    },
    {
      text: "Services",
      type: "group",
      icon: <RoomServiceRounded />,
      items: [
        { name: "Audits", link: "/society/audits" },
        { name: "Consultancies", link: "/society/consultancy" },
        { name: "Financial Institutions", link: "/society/financial" },
        { name: "Legal Practitioners", link: "/society/legal" },
      ],
    },
  ];
  return (
    <div>
      <Toolbar style={{ backgroundColor: "darkgreen" }}>
        <Box
          to="/"
          component={RouterLink}
          display="flex"
          flexDirection="row"
          alignItems="center"
          style={{ textDecoration: "none", color: "white" }}
        >
          <img
            src="../../coat512.png"
            height={50}
            width={50}
            style={{ marginRight: 5 }}
          />
          <Typography variant="h6">Coop-ss</Typography>
        </Box>
      </Toolbar>
      <Divider />
      <List component="nav" aria-label="main mailbox folders">
        {drawerMenu.map(({ type, to, text, items, icon }, index) => {
          switch (type) {
            case "link":
              return (
                <ListItem
                  key={index}
                  selected={index === selectedIndex}
                  component={RouterLink}
                  to={to}
                  button
                  className={classes.drawerItems}
                  onClick={() => {
                    setSelectedIndex(index);
                    setSelectedSubIndex(-1);
                    handleDrawerToggle(false);
                  }}
                >
                  <ListItemIcon>{icon}</ListItemIcon>
                  <ListItemText primary={text} />
                </ListItem>
              );

            case "divider":
              return <Divider key={index} />;
            case "group":
              return (
                <Fragment key={index}>
                  <ListItem
                    button
                    key={index}
                    className={classes.drawerItems}
                    onClick={() => {
                      isOpen === index ? setIsOpen(-1) : setIsOpen(index);
                    }}
                  >
                    <ListItemIcon>{icon}</ListItemIcon>
                    <ListItemText primary={text} />
                    {isOpen === index ? (
                      <ExpandLessRounded />
                    ) : (
                      <ExpandMoreRounded />
                    )}
                  </ListItem>
                  <Collapse in={isOpen === index} timeout="auto" unmountOnExit>
                    <List disablePadding>
                      {items.map(({ name, link }, subIndex) => (
                        <ListItem
                          key={subIndex}
                          button
                          className={`${classes.nested} ${classes.drawerItems}`}
                          component={RouterLink}
                          to={link}
                          selected={
                            selectedSubIndex === subIndex &&
                            selectedIndex === index
                          }
                          onClick={() => {
                            setSelectedSubIndex(subIndex);
                            setSelectedIndex(index);
                            handleDrawerToggle(false);
                          }}
                        >
                          <ListItemText primary={name} />
                        </ListItem>
                      ))}
                    </List>
                  </Collapse>
                </Fragment>
              );
            default:
              return;
          }
        })}
      </List>
    </div>
  );
};

export default SocietyDrawer;
