import {
  LOGIN_SUCCESS,
  LOGIN_FAIL,
  AUTH_ERROR,
  USER_LOADED,
  LOGOUT,
  REGISTER_FAIL,
  REGISTER_SUCCESS,
  CLEAR_ERROR,
  LOADING_USER,
  SET_PATH,
  CLEAR_PATH,
} from "../types";

export default (state, action) => {
  switch (action.type) {
    case LOADING_USER:
      return {
        ...state,
        loading: true,
        isAuthenticated: false,
      };
    case USER_LOADED:
      return {
        ...state,
        ...action.payload,
        isAuthenticated: true,
        loading: false,
        userRole: action.payload.user && action.payload.user.role,
      };

    case REGISTER_SUCCESS:
    case LOGIN_SUCCESS:
      localStorage.setItem("token", action.payload.token);
      return {
        ...state,
        ...action.payload,
        isAuthenticated: true,
        loading: false,
      };
    case REGISTER_FAIL:
    case LOGIN_FAIL:
    case AUTH_ERROR:
      localStorage.removeItem("token");
      return {
        ...state,
        token: null,
        isAuthenticated: false,
        loading: false,
        userRole: null,
        error: action.payload,
      };

    case LOGOUT:
      localStorage.removeItem("token");
      return {
        ...state,
        token: null,
        isAuthenticated: false,
        loading: false,
        userRole: null,
        error: null,
        user: null,
      };

    case CLEAR_ERROR:
      return {
        ...state,
        error: null,
      };
    case SET_PATH:
      return {
        ...state,
        path: action.payload,
      };
    case CLEAR_PATH:
      return {
        ...state,
        path: null,
      };
    default:
      return state;
  }
};
