import React, { useState, Fragment } from "react";
import {
  FormControl,
  InputLabel,
  OutlinedInput,
  InputAdornment,
  IconButton,
  Icon,
  Select,
  MenuItem,
  Button,
  Box,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  root: { paddingTop: theme.spacing(5), paddingBottom: theme.spacing(5) },
  margin: {
    margin: theme.spacing(1),
  },
  textField: {
    width: "-webkit-fill-available",
    minWidth: 120,
  },
}));

const PasswordObj = (props) => {
  const { id, value, label, onChange, minLength } = props;
  const classes = useStyles();
  const [showPassword, setShowPassword] = useState(false);
  const handleClickShowPassword = () => {
    setShowPassword(!showPassword);
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };
  return (
    <FormControl
      className={classes.textField}
      variant="outlined"
      color="primary"
      {...props}
    >
      <InputLabel htmlFor={id}>{label || "Password"}</InputLabel>
      <OutlinedInput
        id={id}
        type={showPassword ? "text" : "password"}
        value={value}
        onChange={onChange}
        minLength={minLength}
        endAdornment={
          <InputAdornment position="end">
            <IconButton
              aria-label="toggle password visibility"
              onClick={handleClickShowPassword}
              onMouseDown={handleMouseDownPassword}
              edge="end"
            >
              {showPassword ? (
                <Icon className="material-icons">visibility</Icon>
              ) : (
                <Icon className="material-icons">visibility_off</Icon>
              )}
            </IconButton>
          </InputAdornment>
        }
        labelWidth={70}
      />
    </FormControl>
  );
};

export const SelectObj = (props) => {
  const classes = useStyles();
  const { id, label, value, onChange, defaultValue, options } = props;
  return (
    <FormControl id={id} variant="outlined" className={classes.textField}>
      <InputLabel id={id + "-label"}>{label}</InputLabel>
      <Select
        labelId={id + "-label"}
        id={id}
        name={id}
        value={value}
        onChange={onChange}
        label={label}
      >
        {options.map(({ value, label }, index) => {
          return (
            <MenuItem key={index} value={value || index}>
              {value === defaultValue ? <em>{label}</em> : <span>{label}</span>}
            </MenuItem>
          );
        })}
      </Select>
    </FormControl>
  );
};

export const FileButton = (props) => {
  const { id, color, text, accept, onChange, value, multiple } = props;
  return (
    <Box width={1}>
      <Box px={3}>{value && text}</Box>
      <Box display="none">
        <input
          onChange={onChange}
          accept={accept}
          id={id}
          multiple={multiple}
          type="file"
        />
      </Box>
      <label htmlFor={id}>
        <Box display="flex" alignItems="center">
          <Box px={3} flexGrow={1} fontStyle={24}>
            {value ? value : text}
          </Box>
          <Button variant="contained" color={color} component="span">
            Choose File
          </Button>
        </Box>
      </label>
    </Box>
  );
};

export default PasswordObj;
