const role = ["legal", "financial", "intauditor", "extauditor", "consultant"];

const Profile = () => {
  const { user, userRole } = React.useContext(AuthContext);
  const [details, setdetails] = React.useState(user || {});

  React.useEffect(() => {
    user && setdetails(user);
  }, [user]);

  const page = Dashboard();

  if (!role.includes(userRole)) return <Redirect to={page} />;

  return (
    <PageWrapper>
      <Box py={5}>
        <Container maxWidth="md">
          <Typography variant="h2" color="initial" gutterBottom>
            Profile
          </Typography>
          <List>
            {Object.keys(details).map(
              (detail, index) =>
                template[`${detail}`] && (
                  <React.Fragment key={index}>
                    <ListItem>
                      <ListItemText
                        primary={template[`${detail}`].label}
                        secondary={
                          <SecondaryItem
                            {...{
                              type: template[`${detail}`].type,
                              value: details[`${detail}`],
                              options: template[`${detail}`].options,
                            }}
                          />
                        }
                      />
                    </ListItem>
                    <Divider />
                  </React.Fragment>
                )
            )}
          </List>
        </Container>
      </Box>
    </PageWrapper>
  );
};

export default Profile;
