import React from "react";
import { Skeleton } from "@material-ui/lab";
import { InsertDriveFileRounded, AddRounded } from "@material-ui/icons";
import { Grid, Typography, Avatar } from "@material-ui/core";

const MySkeletons = (props) => {
  return <Skeleton variant="rect" height={props.height * 60} width="100%" />;
};

export const FileBox = (props) => {
  if (props.type === "add") {
    return (
      <Grid spacing={2} alignItems="center" container wrap="nowrap">
        <Grid item>
          <Avatar>
            <AddRounded />
          </Avatar>
        </Grid>
        <Grid item>
          <Typography variant="body1" color="initial">
            {props.name}
          </Typography>
        </Grid>
      </Grid>
    );
  }
  return (
    <Grid spacing={2} container wrap="nowrap">
      <Grid item>
        <Avatar>
          <InsertDriveFileRounded />
        </Avatar>
      </Grid>
      <Grid item>
        <Typography variant="body1" color="initial">
          {props.name}
        </Typography>
        <Typography variant="body2" color="initial">
          {new Date(props.dateCreated).toDateString()}
        </Typography>
      </Grid>
    </Grid>
  );
};

export default MySkeletons;
