import React, { useReducer } from "react";
import NotificationsContext from "./notificationsContext";
import notificationsReducer from "./notificationsReducer";

import axios from "axios";

import {
  GET_ALL_NOTIFICATIONS,
  READ_ALL_NOTIFICATIONS,
  READ_NOTIFICATION,
  DELETE_NOTIFICATION,
  SET_CURRENT,
  CLEAR_CURRENT,
} from "../types";

const NotificationsState = (props) => {
  const initialState = {
    notifications: [],
    newNotifications: [],
    currentNotification: null,
  };
  const [state, dispatch] = useReducer(notificationsReducer, initialState);
  const config = {
    headers: {
      "Content-Type": "application/json",
    },
  };

  // GET ALL NOTIFICATIONS
  const getAllNotifications = async () => {
    try {
      const res = await axios.get("/api/notifications", config);

      dispatch({
        type: GET_ALL_NOTIFICATIONS,
        payload: res.data,
      });
      return;
    } catch (error) {
      console.error(error);
      return;
    }
  };

  // READ ALL NOTIFICATIONS
  const readAllNotification = async () => {
    console.log("read all");
    try {
      const res = await axios.post("/api/notifications", config);
      dispatch({
        type: READ_ALL_NOTIFICATIONS,
        payload: res.data,
      });
      return;
    } catch (error) {
      console.error(error);
      return;
    }
  };

  // READ NOTIFICATION
  const readNotification = async (id) => {
    try {
      const res = await axios.post("/api/notifications/read/" + id, config);
      dispatch({
        type: READ_NOTIFICATION,
        payload: res.data,
      });
      return;
    } catch (error) {
      console.error(error);
      return;
    }
  };

  // DELETE NOTIFICATION
  const deleteNotification = async (id) => {
    try {
      const res = await axios.post("/api/notifications/delete/" + id, config);
      dispatch({
        type: DELETE_NOTIFICATION,
        payload: res.data,
      });
      return;
    } catch (error) {
      console.error(error);
      return;
    }
  };

  // SET CURRENT
  const setCurrentNotification = (notice) => {
    dispatch({
      type: SET_CURRENT,
      payload: notice,
    });
  };

  // CLEAR CURRENT
  const clearCurrentNotification = () => {
    dispatch({
      type: CLEAR_CURRENT,
    });
  };

  return (
    <NotificationsContext.Provider
      value={{
        notifications: state.notifications,
        newNotifications: state.newNotifications,
        currentNotification: state.currentNotification,
        getAllNotifications,
        readAllNotification,
        readNotification,
        deleteNotification,
        setCurrentNotification,
        clearCurrentNotification,
      }}
    >
      {props.children}
    </NotificationsContext.Provider>
  );
};

export default NotificationsState;
