import React, { useContext, useEffect } from "react";
import PageWrapper from "../../components/layout/Wrapper/PageWrapper";
import {
  Box,
  Container,
  Typography,
  IconButton,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions,
} from "@material-ui/core";
import { Link } from "react-router-dom";
import { ArrowBackRounded, CloudDownloadRounded } from "@material-ui/icons";
import fileContext from "../../components/context/files/fileContext";
import { DataGrid } from "@material-ui/data-grid";
import UploadResources from "../UploadResources";
import AlertContext from "../../components/context/alert/alertContext";
import Axios from "axios";

const Elearning = () => {
  const { getResources, resources } = useContext(fileContext);
  const { setAlert } = React.useContext(AlertContext);

  const [id, setId] = React.useState(null);
  const [open, setOpen] = React.useState(false);
  useEffect(() => {
    getResources();
  }, []);
  const [display, setDisplay] = React.useState([]);

  React.useEffect(() => {
    if (id) {
      setOpen(true);
    } else {
      setOpen(false);
    }
    return () => {
      setId(null);
    };
  }, [id]);

  const deleteResource = async (_id) => {
    try {
      await Axios.delete(`/api/resources/${id}`);
      getResources();
    } catch (err) {
      console.log(err);
      setAlert("Oops! We couldn't delete the resource", "error");
    }
    setId(null);
  };

  useEffect(() => {
    Array.isArray(resources) &&
      setDisplay(resources.filter((file) => file.type === "E-learning"));
  }, [resources]);

  return (
    <PageWrapper>
      <Box py={5}>
        <Container maxWidth="md">
          <Box display="flex" flexDirection="row">
            <Box width={1} display="flex">
              <IconButton
                style={{ marginLeft: 20, height: 50, width: 50 }}
                component={Link}
                to="/admin"
              >
                <ArrowBackRounded />
              </IconButton>
              <Typography variant="h3" color="initial" gutterBottom noWrap>
                E-Learning
              </Typography>
            </Box>
            <Box
              width={1}
              display="flex"
              justifyContent="flex-end"
              alignItems="center"
            >
              <UploadResources type="E-learning" />
            </Box>
          </Box>
          <Box height={400}>
            <DataGrid
              rows={display.map((resource, index) => {
                return {
                  ...resource,
                  id: resource._id,
                };
              })}
              columns={[
                { field: "title", headerName: "Title", width: 250 },
                {
                  field: "details",
                  headerName: "Description",
                  width: 400,
                },
                {
                  field: "dateUploaded",
                  headerName: "Date Uploaded",
                  width: 120,
                  valueGetter: (params) =>
                    new Date(params.value).toLocaleDateString(),
                },
                {
                  field: "url",
                  headerName: "Download",
                  width: 100,
                  renderCell: (params) => (
                    <IconButton
                      component="a"
                      color="primary"
                      href={params.value}
                      target="_blank"
                    >
                      <CloudDownloadRounded />
                    </IconButton>
                  ),
                },
              ]}
              onRowSelected={(row) => setId(row.data.id)}
            />
          </Box>
        </Container>
      </Box>
      <Dialog open={open} onClose={() => setOpen(false)}>
        <DialogTitle>Delete document?</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Remove the data of this resource from the database.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => deleteResource(id)} color="primary">
            Yes
          </Button>
          <Button onClick={() => setOpen(false)} color="default">
            No
          </Button>
        </DialogActions>
      </Dialog>
    </PageWrapper>
  );
};

export default Elearning;
