import React, { useReducer } from "react";
import AuthContext from "./authContext";
import authReducer from "./authReducer";

import axios from "axios";
import setAuthToken from "../../../utils/setAuthToken";

import {
  LOGIN_SUCCESS,
  LOGIN_FAIL,
  AUTH_ERROR,
  USER_LOADED,
  LOGOUT,
  REGISTER_FAIL,
  REGISTER_SUCCESS,
  CLEAR_ERROR,
  LOADING_USER,
  SET_PATH,
  CLEAR_PATH,
} from "../types";

axios.defaults.baseURL = "https://www.coop-ss.com.ng/";

const AuthState = (props) => {
  const initialState = {
    token: localStorage.getItem("token"),
    isAuthenticated: false,
    loading: false,
    userRole: null,
    error: null,
    user: null,
    notifications: null,
    details: null,
    path: null,
  };
  const [state, dispatch] = useReducer(authReducer, initialState);
  const config = {
    headers: {
      "Content-Type": "application/json",
    },
  };

  // LOAD USER
  const loadUser = async () => {
    // Load token into global headers
    if (localStorage.token) {
      setAuthToken(localStorage.token);
      loadingUser();
    } else {
      return;
    }

    try {
      const res = await axios.get("/api/auth");
      dispatch({
        type: USER_LOADED,
        payload: res.data,
      });
    } catch (error) {
      dispatch({
        type: AUTH_ERROR,
        payload:
          (error.response &&
            ((error.response.data && error.response.data.msg) ||
              error.response.statusText)) ||
          "Runtime Error",
      });
      setTimeout(() => {
        clearErrors();
      }, 500);
    }
  };
  // REGISTER USER
  const register = async (formData) => {
    loadingUser();

    try {
      const res = await axios.post("/api/users", formData, config);

      dispatch({
        type: REGISTER_SUCCESS,
        payload: res.data,
      });
      loadUser();
    } catch (error) {
      dispatch({
        type: REGISTER_FAIL,
        payload:
          (error.response &&
            ((error.response.data && error.response.data.msg) ||
              error.response.statusText)) ||
          "Runtime Error",
      });
      setTimeout(() => {
        clearErrors();
      }, 500);
    }
  };

  // LOGIN USER
  const login = async (formData) => {
    loadingUser();
    console.log(formData, "Form Data");

    try {
      const res = await axios.post("/api/auth", formData, config);
      dispatch({
        type: LOGIN_SUCCESS,
        payload: res.data,
      });
      loadUser();
    } catch (error) {
      dispatch({
        type: LOGIN_FAIL,
        payload:
          (error.response &&
            ((error.response.data && error.response.data.msg) ||
              error.response.statusText)) ||
          "Runtime Error",
      });
      setTimeout(() => {
        clearErrors();
      }, 500);
    }
  };

  // LOGOUT USER
  const logout = () => {
    dispatch({
      type: LOGOUT,
    });
  };
  // CLEAR ERRORS
  const clearErrors = () => {
    dispatch({
      type: CLEAR_ERROR,
    });
  };

  const loadingUser = () => {
    dispatch({
      type: LOADING_USER,
    });
  };
  // SET PATH
  const setPath = (path) => {
    dispatch({
      type: SET_PATH,
      payload: path,
    });
  };
  // CLEAR PATH
  const clearPath = () => {
    dispatch({
      type: CLEAR_PATH,
    });
  };
  // GET PATH
  const getPath = () => {
    const path = state.path;
    clearPath();
    return path;
  };

  return (
    <AuthContext.Provider
      value={{
        token: state.token,
        isAuthenticated: state.isAuthenticated,
        loading: state.loading,
        userRole: state.userRole,
        error: state.error,
        user: state.user,
        path: state.path,
        details: state.details,
        notifications: state.notifications,
        loadUser,
        register,
        login,
        logout,
        clearErrors,
        getPath,
        setPath,
        clearPath,
      }}
    >
      {props.children}
    </AuthContext.Provider>
  );
};

export default AuthState;
