import React from "react";
import {
  Box,
  Container,
  Card,
  CardMedia,
  Grid,
  Input,
  makeStyles,
  Select,
  MenuItem,
  Typography,
  CardContent,
  Button,
  IconButton,
  CircularProgress,
} from "@material-ui/core";

import { KeyboardDatePicker } from "@material-ui/pickers";
import { ArrowBackRounded } from "@material-ui/icons";
import { useParams } from "react-router-dom";
import Axios from "axios";
import AlertContext from "../components/context/alert/alertContext";
import SearchUsers from "../components/layout/SearchUsers";
import PageWrapper from "../components/layout/Wrapper/PageWrapper";
import { orange } from "@material-ui/core/colors";
import Background from "../resources/background.png";

// cell props- required, type [short, long, dropdown, radio, checkbox, time, date, file, user], label, value, options, visible:{propName, value}, propName

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: "#fafafa",
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
  fullWidth: { width: "100%" },
  minWidth: {
    width: 200,
  },
  card: {
    width: "100%",
  },
  cardContent: {
    height: 200,
  },
  none: {
    display: "none",
  },
  alignLeft: {
    justifyContent: "flex-end",
    display: "flex",
  },
  gray: {
    color: "gray",
    fontWeight: "bold",
    marginBottom: 10,
  },
}));

const Main = (props) => {
  const { id } = useParams();
  const { setAlert } = React.useContext(AlertContext);
  const classes = useStyles();
  const [loading, setLoading] = React.useState(true);
  const [error, setError] = React.useState(null);
  const [obj, setObj] = React.useState([]);
  const [link, setLink] = React.useState("");
  React.useEffect(() => {
    loadForm();
  }, []);

  const [title, setTitle] = React.useState("");
  const [desc, setDesc] = React.useState("");
  const [changeProfile, setChangeProfile] = React.useState(null);
  const [receiverId, setReceiverId] = React.useState(null);
  const [toAdmin, setToAdmin] = React.useState(null);
  const [roleLabel, setRoleLabel] = React.useState("");
  const [receiverRole, setReceiverRole] = React.useState("");

  const switchRole = (role) => {
    switch (role) {
      case "society":
        return "Society";
      case "intauditor":
      case "extauditor":
        return "Auditor";
      case "consultant":
        return "Consultant";
      case "financial":
        return "Financial Institution";
      case "legal":
        return "Legal Practitioners";
      default:
        return "";
    }
  };

  const loadForm = async () => {
    setLoading(true);
    try {
      const res = await Axios.get(`/api/requests/form/${id}`);
      console.log(res.data);
      setObj(res.data.content);
      setTitle(res.data.title);
      setDesc(res.data.description);
      setLink(res.data.postLink);
      setChangeProfile(res.data.changeProfile);
      setReceiverId(res.data.receiverId);
      setToAdmin(res.data.toAdmin);
      setReceiverRole(res.data.receiverRole);
      setRoleLabel(switchRole(res.data.receiverRole));
    } catch (err) {
      console.log(err);
    }
    setLoading(false);
  };

  const goBack = props.history.goBack;

  const onClick = async (filledForm) => {
    setAlert("Please Wait...");
    try {
      const raw = filledForm.filter((field) => field.value);
      const files = raw.filter((item) => item.type === "file");
      let content = raw;
      const cloud = process.env.REACT_APP_CLOUD;
      const form = new FormData();
      form.append("upload_preset", process.env.REACT_APP_UPLOAD_PRESET);
      form.append("cloud_name", cloud);
      form.append("folder", process.env.REACT_APP_FOLDER);
      const upload_url = `https://api.cloudinary.com/v1_1/${cloud}/auto/upload`;
      for (let index = 0; index < files.length; index++) {
        const item = files[index];
        form.append("file", item.value);
        await fetch(upload_url, {
          method: "POST",
          body: form,
        })
          .then((res) => res.json())
          .then((response) => {
            content = raw.map((edit) => {
              if (edit.propName === item.propName) {
                return { ...edit, value: response.secure_url };
              } else {
                return edit;
              }
            });
          });
      }
      await Axios.post(link, {
        title,
        description: desc,
        changeProfile,
        receiverId,
        toAdmin,
        content,
      });
      setAlert("Form Submitted!", "success");
      goBack();
    } catch (err) {
      console.log(err);
      const msg = err.response && err.response.data && err.response.data.msg;
      setAlert(msg || "Runtime Error", "error");
    }
  };

  return (
    <PageWrapper>
      <FormPage
        {...{
          classes,
          goBack,
          title,
          desc,
          loading,
          error,
          obj,
          setAlert,
          onClick,
          roleLabel,
          receiverRole,
          toAdmin,
          receiverId,
          setReceiverId,
        }}
      />
    </PageWrapper>
  );
};

class FormPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      objState: this.props.obj,
      loading: false,
    };
  }

  shouldComponentUpdate(nextProps, nextState) {
    if (this.props !== nextProps) {
      this.setState({
        objState: this.props.obj,
        loading: false,
      });
    }
    return (
      this.props !== nextProps ||
      this.state.objState !== nextState.objState ||
      this.state.objState.length !== nextState.objState.length ||
      Boolean(
        this.state.objState.filter(
          (item, index) => item.value !== nextState.objState[index].value
        )
      )
    );
  }

  onChange = (val, index) => {
    console.log(val, index);
    let objMini = this.state.objState;
    objMini[index] = { ...objMini[index], value: val };
    const state = this.state;
    this.setState({ ...state, objState: objMini });
  };

  onClick = async () => {
    this.setState({ ...this.state, loading: true });
    const required = this.state.objState.find(
      (item) => item.required && !item.value
    );
    const { toAdmin, roleLabel, receiverId, setAlert } = this.props;
    if (required) {
      this.props.setAlert(`Fill in the value for ${required.label}`, "warning");
    } else if (!toAdmin && roleLabel && !receiverId) {
      setAlert(`Select a(n) ${roleLabel}`);
    } else {
      const state = this.state;
      this.setState({
        ...state,
        loading: true,
      });
      await this.props.onClick(this.state.objState);
      this.setState({
        ...state,
        loading: false,
      });
    }
    this.setState({ ...this.state, loading: false });
  };

  render() {
    const classes = this.props.classes;
    const { roleLabel, receiverRole, toAdmin, setReceiverId } = this.props;
    if (this.props.loading)
      return (
        <Box
          py={2}
          px={0}
          className={classes.root}
          height={500}
          display="flex"
          alignItems="center"
          justifyContent="center"
        >
          <CircularProgress />
          <Typography variant="body2" color="initial">
            Loading...
          </Typography>
        </Box>
      );
    return (
      <Box
        py={5}
        px={0}
        className={classes.root}
        height="100%"
        bgcolor="background.paper"
      >
        <Container maxWidth="sm">
          <Grid
            container
            className={classes.fullWidth}
            direction="column"
            justify="space-between"
            alignItems="stretch"
            spacing={2}
          >
            <Grid item>
              <Card>
                <CardMedia
                  image={Background}
                  subImage="https://i.picsum.photos/id/546/2000/2000.jpg?hmac=fs9f4o7uCopC9dIOeYrAsf4c7Wswoi6XkYIP7t9zDCg"
                >
                  <CardContent>
                    <Box
                      color="primary.contrastText"
                      display="flex"
                      justifyContent="flex-end"
                    >
                      <IconButton color="inherit" onClick={this.props.goBack}>
                        <ArrowBackRounded />
                      </IconButton>
                    </Box>
                    <Box pb={3} color="primary.contrastText">
                      <Typography variant="h5" color="inherit" gutterBottom>
                        {this.props.title}
                      </Typography>
                      <Typography variant="subtitle1" color="inherit">
                        {this.props.desc}
                      </Typography>
                    </Box>
                  </CardContent>
                </CardMedia>
              </Card>
            </Grid>
            {this.state.objState.map((object, index) => (
              <Grid key={index} item>
                <Card
                  style={{
                    backgroundColor:
                      object.type === "header" ? orange[300] : "white",
                  }}
                >
                  <CardContent>
                    <Cell {...{ ...object, index, onChange: this.onChange }} />
                  </CardContent>
                </Card>
              </Grid>
            ))}
            {!toAdmin && roleLabel && (
              <Grid item>
                <Card>
                  <CardContent>
                    <Typography
                      variant="body2"
                      color="initial"
                      gutterBottom
                      className={classes.gray}
                    >
                      {roleLabel}
                    </Typography>
                    <SearchUsers
                      onSelect={(val) => {
                        console.log("User Value...", val);
                        val && setReceiverId(val.userId);
                      }}
                      role={receiverRole}
                      label="Search"
                    />
                  </CardContent>
                </Card>
              </Grid>
            )}
            <Grid item classNa cxme={classes.alignLeft}>
              <Button
                variant="contained"
                color="primary"
                disabled={this.state.loading}
                onClick={this.onClick}
              >
                Submit
              </Button>
            </Grid>
          </Grid>
        </Container>
      </Box>
    );
  }
}

const Cell = ({
  index,
  required,
  type,
  label,
  value,
  options,
  visible,
  propName,
  onChange,
  placeholder,
}) => {
  const classes = useStyles();
  let field;
  switch (type) {
    case "text":
      field = (
        <Input
          id={propName}
          placeholder={placeholder}
          value={value}
          onChange={(e) => {
            onChange(e.target.value, index);
          }}
          className={classes.fullWidth}
        />
      );
      break;
    case "dropdown":
      field = (
        <Select
          id={propName}
          value={value}
          className={classes.minWidth}
          onChange={(e) => onChange(e.target.value, index)}
        >
          {options.map((val) => (
            <MenuItem value={val} key={val}>
              {val}
            </MenuItem>
          ))}
        </Select>
      );
      break;
    case "date":
      field = (
        <KeyboardDatePicker
          fullWidth
          variant="inline"
          format={(options && options.format) || "DD/MM/yyyy"}
          margin="normal"
          id={propName}
          value={value}
          onChange={(val) => onChange(val, index)}
          KeyboardButtonProps={{
            "aria-label": "change date",
          }}
        />
      );
      break;
    case "file":
      console.log(value);
      field = (
        <div>
          <input
            accept={(options && options.accept) || "*"}
            className={classes.none}
            id={propName}
            type="file"
            onChange={(e) => {
              console.log(e.target.files[0]);
              onChange(e.target.files[0], index);
            }}
          />
          <label htmlFor={propName}>
            <Button variant="outlined" color="primary" component="span">
              {(value && value.name) || "Select file"}
            </Button>
          </label>
        </div>
      );
      break;
    case "user":
      field = (
        <React.Fragment>
          <SearchUsers
            {...(options || {})}
            onSelect={(e, val) => onChange(val, index)}
          />
        </React.Fragment>
      );
      break;
    case "header":
      field = (
        <React.Fragment>
          <Typography variant="body1" color="initial" gutterBottom>
            {options && options[0]}
          </Typography>
          {options &&
            options.length > 1 &&
            options
              .filter((val, index) => index > 0)
              .map((val) => (
                <Typography variant="subtitle1" color="initial" gutterBottom>
                  {val}
                </Typography>
              ))}
        </React.Fragment>
      );
      break;
    default:
      break;
  }

  return (
    <div>
      {label && (
        <Typography
          variant="body2"
          color="initial"
          gutterBottom
          className={classes.gray}
        >
          {label}
        </Typography>
      )}
      {field}
    </div>
  );
};

export default Main;
