import React from "react";
import PageWrapper from "../components/layout/Wrapper/PageWrapper";
import {
  Box,
  Container,
  Typography,
  IconButton,
  Dialog,
  DialogContentText,
  Button,
  DialogTitle,
  DialogContent,
  DialogActions,
  Paper,
  TextField,
  Grid,
  InputAdornment,
} from "@material-ui/core";
import { Link, Redirect } from "react-router-dom";
import { ArrowBackRounded, SearchRounded } from "@material-ui/icons";

import { makeStyles, withStyles } from "@material-ui/core/styles";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import { DataGrid } from "@material-ui/data-grid";
import Axios from "axios";
import AlertContext from "../components/context/alert/alertContext";
import { Autocomplete } from "@material-ui/lab";
import AuthContext from "../components/context/auth/authContext";

const states = [
  "Abia",
  "Adamawa",
  "Akwa Ibom",
  "Anambra",
  "Bauchi",
  "Bayelsa",
  "Benue",
  "Borno",
  "Cross River",
  "Delta",
  "Ebonyi",
  "Edo",
  "Ekiti",
  "Enugu",
  "Gombe",
  "Imo",
  "Jigawa",
  "Kaduna",
  "Kano",
  "Katsina",
  "Kebbi",
  "Kogi",
  "Kwara",
  "Lagos",
  "Nasarawa",
  "Niger",
  "Ogun",
  "Ondo",
  "Osun",
  "Oyo",
  "Plateau",
  "Rivers",
  "Sokoto",
  "Taraba",
  "Yobe",
  "Zamfara",
  "Federal Capital Territory",
];

const AntTabs = withStyles((theme) => ({
  root: {
    borderBottom: "1px solid #e8e8e8",
  },
  indicator: {
    backgroundColor: theme.palette.primary.main,
  },
}))(Tabs);

const AntTab = withStyles((theme) => ({
  root: {
    textTransform: "none",
    minWidth: 72,
    fontWeight: theme.typography.fontWeightRegular,
    marginRight: theme.spacing(4),
    fontFamily: [
      "-apple-system",
      "BlinkMacSystemFont",
      '"Segoe UI"',
      "Roboto",
      '"Helvetica Neue"',
      "Arial",
      "sans-serif",
      '"Apple Color Emoji"',
      '"Segoe UI Emoji"',
      '"Segoe UI Symbol"',
    ].join(","),
    "&:hover": {
      color: theme.palette.primary.light,
      opacity: 1,
    },
    "&$selected": {
      color: theme.palette.primary.dark,
      fontWeight: theme.typography.fontWeightMedium,
    },
    "&:focus": {
      color: theme.palette.primary.light,
    },
  },
  selected: {},
}))((props) => <Tab disableRipple {...props} />);

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  padding: {
    padding: theme.spacing(3),
  },
  demo1: {
    backgroundColor: theme.palette.background.default,
  },
  demo2: {
    backgroundColor: "#2e1534",
  },
}));

const Directory = () => {
  const classes = useStyles();
  const [id, setId] = React.useState(null);
  const [open, setOpen] = React.useState(false);
  const [value, setValue] = React.useState(0);
  const [users, setUsers] = React.useState({
    auditors: [],
    financials: [],
    societies: [],
    legals: [],
    consultants: [],
  });
  const [rows, setRows] = React.useState([]);
  const [display, setDisplay] = React.useState([]);
  const [state, setState] = React.useState("");
  const [search, setSearch] = React.useState("");

  const [loading, setLoading] = React.useState(true);
  const { setAlert } = React.useContext(AlertContext);
  const { isAuthenticated, user } = React.useContext(AuthContext);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  React.useEffect(() => {
    if (id) {
      setOpen(true);
    } else {
      setOpen(false);
    }
    return () => {
      setId(null);
    };
  }, [id]);

  const getUsers = async () => {
    setLoading(true);
    try {
      const res = await Axios.get("/api/users/directory");
      console.log(res.data);
      setUsers(res.data);
    } catch (err) {
      console.log(err);
    }
    setLoading(false);
  };

  React.useEffect(() => {
    getUsers();
  }, []);

  React.useEffect(() => {
    setDisplay(
      rows.filter(
        (row) =>
          (!state || row.state === state) &&
          (!search ||
            (row.lga && row.lga.toLowerCase().includes(search.toLowerCase())) ||
            (row.name &&
              row.name.toLowerCase().includes(search.toLowerCase())) ||
            (row.contactPhone &&
              row.contactPhone.toLowerCase().includes(search.toLowerCase())) ||
            (row.contactEmail &&
              row.contactEmail.toLowerCase().includes(search.toLowerCase())))
      )
    );
  }, [rows, search, state]);

  React.useEffect(() => {
    switch (value) {
      case 0:
        setRows(users.societies);
        break;
      case 1:
        setRows(users.auditors);
        break;
      case 2:
        setRows(users.financials);
        break;
      case 3:
        setRows(users.legals);
        break;
      case 4:
        setRows(users.consultants);
        break;
    }
  }, [users, value]);

  if (
    !user ||
    !["admin", "superAdmin", "society"].includes(user.role) ||
    (!user.approved && user.role === "society")
  )
    return <Redirect to="/login" />;

  return (
    <PageWrapper>
      <Box py={5}>
        <Container maxWidth="md">
          <Grid
            container
            direction="row"
            justify="space-between"
            alignItems="center"
            spacing={2}
          >
            <Grid item xs={12} md={6}>
              <Typography variant="h3" color="initial" gutterBottom>
                Inter-State Directory
              </Typography>
            </Grid>
            <Grid item xs={12} md={3}>
              <TextField
                placeholder="Search"
                value={search}
                variant="outlined"
                onChange={(e) => setSearch(e.currentTarget.value)}
                size="small"
                fullWidth
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <SearchRounded color="disabled" />
                    </InputAdornment>
                  ),
                }}
              />
            </Grid>
            <Grid item xs={12} md={3}>
              <Autocomplete
                value={state}
                onChange={(event, newValue) => {
                  setState(newValue);
                }}
                id="autocomplete-state"
                options={states}
                renderInput={(params) => (
                  <TextField
                    {...params}
                    label="State"
                    variant="outlined"
                    size="small"
                    fullWidth
                  />
                )}
              />
            </Grid>
          </Grid>
          <div className={classes.demo1}>
            <AntTabs
              value={value}
              onChange={handleChange}
              aria-label="interstate-tab"
              scrollButtons="auto"
            >
              <AntTab label="Societies" />
              <AntTab label="General Auditors" />
              <AntTab label="Financial Institutions" />
              <AntTab label="Legal Institutions" />
              <AntTab label="Consultants" />
            </AntTabs>
            <Typography className={classes.padding} />
          </div>
          <Paper elevation={1}>
            <Box height={400}>
              <DataGrid
                loading={loading}
                rows={display.map((row) => {
                  return {
                    ...row,
                    id: row._id,
                    contactPhone:
                      row.contactPhone ||
                      row.phone ||
                      (row.userId && row.userId.phone),
                    contactEmail:
                      row.contactEmail ||
                      row.email ||
                      (row.userId && row.userId.email),
                  };
                })}
                columns={[
                  { field: "name", headerName: "Name", width: 400 },
                  { field: "contactEmail", headerName: "Email", width: 300 },
                  { field: "contactPhone", headerName: "Phone", width: 300 },
                  { field: "state", headerName: "State", width: 300 },
                  { field: "lga", headerName: "L.G.A.", width: 300 },
                ]}
                onRowSelected={(row) => setId(row.data.id)}
              />
            </Box>
          </Paper>
        </Container>
      </Box>
    </PageWrapper>
  );
};

export default Directory;
