import React from "react";
import PageWrapper from "../../components/layout/Wrapper/PageWrapper";
import { Box, Container, Typography, IconButton } from "@material-ui/core";
import { Link, useHistory } from "react-router-dom";
import { ArrowBackRounded } from "@material-ui/icons";

import { makeStyles, withStyles } from "@material-ui/core/styles";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import { DataGrid } from "@material-ui/data-grid";
import Axios from "axios";

const AntTabs = withStyles((theme) => ({
  root: {
    borderBottom: "1px solid #e8e8e8",
  },
  indicator: {
    backgroundColor: theme.palette.primary.main,
  },
}))(Tabs);

const AntTab = withStyles((theme) => ({
  root: {
    textTransform: "none",
    minWidth: 72,
    fontWeight: theme.typography.fontWeightRegular,
    marginRight: theme.spacing(4),
    fontFamily: [
      "-apple-system",
      "BlinkMacSystemFont",
      '"Segoe UI"',
      "Roboto",
      '"Helvetica Neue"',
      "Arial",
      "sans-serif",
      '"Apple Color Emoji"',
      '"Segoe UI Emoji"',
      '"Segoe UI Symbol"',
    ].join(","),
    "&:hover": {
      color: theme.palette.primary.light,
      opacity: 1,
    },
    "&$selected": {
      color: theme.palette.primary.dark,
      fontWeight: theme.typography.fontWeightMedium,
    },
    "&:focus": {
      color: theme.palette.primary.light,
    },
  },
  selected: {},
}))((props) => <Tab disableRipple {...props} />);

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  padding: {
    padding: theme.spacing(1),
  },
  demo1: {
    backgroundColor: theme.palette.background.default,
  },
  demo2: {
    backgroundColor: "#2e1534",
  },
}));

const Requests = () => {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);
  const [requests, setRequests] = React.useState([]);
  const [rows, setRows] = React.useState([]);
  const [loading, setLoading] = React.useState(true);

  const history = useHistory();

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const getRequests = async () => {
    setLoading(true);
    try {
      const res = await Axios.get("/api/requests/sent");
      console.log(res.data);
      setRequests(res.data);
    } catch (err) {
      console.log(err);
    }
    setLoading(false);
  };

  React.useEffect(() => {
    getRequests();
  }, []);

  React.useEffect(() => {
    switch (value) {
      case 0:
        setRows(requests.filter((req) => req.status === "Pending"));
        break;
      case 1:
        setRows(requests.filter((req) => req.status === "Approved"));
        break;
      default:
        setRows(requests.filter((req) => req.status === "Cancelled"));
        break;
    }
  }, [requests, value]);

  return (
    <PageWrapper>
      <Box py={5}>
        <Container maxWidth="md">
          <Box display="flex" flexDirection="row">
            <IconButton
              style={{ marginLeft: 20, height: 50, width: 50 }}
              component={Link}
              to="/admin"
            >
              <ArrowBackRounded />
            </IconButton>
            <Typography variant="h3" color="initial" gutterBottom>
              Requests
            </Typography>
          </Box>
          <div className={classes.demo1}>
            <AntTabs
              value={value}
              onChange={handleChange}
              aria-label="ant example"
            >
              <AntTab label="Pending" />
              <AntTab label="Approved" />
              <AntTab label="Cancelled" />
            </AntTabs>
            <Typography className={classes.padding} />
          </div>
          <Box height={400}>
            <DataGrid
              loading={loading}
              rows={rows.map((row) => {
                return {
                  ...row,
                  id: row._id,
                  sender: row.senderId && row.senderId.name,
                };
              })}
              columns={[
                { field: "sender", headerName: "Sender", width: 200 },
                { field: "title", headerName: "Title", width: 300 },
                { field: "description", headerName: "Description", width: 500 },
                { field: "status", headerName: "Status", width: 150 },
                {
                  field: "dateCreated",
                  headerName: "Date",
                  width: 200,
                  valueGetter: (params) =>
                    new Date(params.value).toLocaleDateString(),
                },
              ]}
              onRowClick={(params) =>
                history.push(`/requests/${params.row.id}`)
              }
            />
          </Box>
        </Container>
      </Box>
    </PageWrapper>
  );
};

export default Requests;
