import React, { useContext, useEffect } from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
} from "react-router-dom";
import "./App.css";
import { createMuiTheme, ThemeProvider } from "@material-ui/core/styles";
import { green, orange } from "@material-ui/core/colors";
import Home from "./pages/Home";
import AboutUs from "./pages/AboutUs";
import News from "./pages/News";
import PageNotFound from "./pages/PageNotFound";
import Login from "./pages/Login";
import Register from "./pages/Register";
import Notifications from "./pages/Notifications";
import Profile, {
  MandatoryReturns,
  Requests,
  Consultancy,
  Financial,
  Legal,
  Audits,
  Members,
} from "./pages/societypages/Cooperative";
import SocietyUpdate from "./components/layout/Updates/SocietyUpdate";

import Alerts from "./components/layout/Alerts";
import setAuthToken from "./utils/setAuthToken";

import MomentUtils from "@date-io/moment";
import { MuiPickersUtilsProvider } from "@material-ui/pickers";

import AuthContext from "./components/context/auth/authContext";
import NotificationsContext from "./components/context/notifications/notificationsContext";

import PrivateRoute from "./components/routing/PrivateRouting";
import Resources from "./pages/Resources";
import Admin from "./pages/adminpages/Admin";
import Analytics from "./pages/adminpages/Analytics";
import Users from "./pages/adminpages/Users";
import EditResources from "./pages/adminpages/Resources";
import AdminRequests from "./pages/adminpages/Requests";
import FormPage from "./pages/FormPage";
import RequestsPage from "./pages/Requests";
import AdminRequestsPage from "./pages/adminpages/RequestPage";
import AdminElearning from "./pages/adminpages/ELearning";
import Elearning from "./pages/ELearning";

import AuditMain from "./pages/auditorpages/Main";
import ConsultantMain from "./pages/consultantpages/Main";
import LegalMain from "./pages/legalpages/Main";
import FinancialMain from "./pages/financialpages/Main";
import AuditRequests from "./pages/auditorpages/Requests";
import ForgotPassword from "./pages/ForgotPassword";
import Directory from "./pages/Directory";

import UserProfile from "./pages/Profile";

const theme = createMuiTheme({
  palette: {
    primary: {
      main: green[800],
    },
    secondary: {
      main: orange[300],
    },
    bgcolor: {
      main: "#fafafa",
    },
    white: {
      main: "#fff",
    },
  },
});

localStorage.token && setAuthToken(localStorage.token);

function App() {
  const authContext = useContext(AuthContext);
  const notificationsContext = useContext(NotificationsContext);
  useEffect(() => {
    authContext.loadUser();
  }, []);
  useEffect(() => {
    if (authContext.user) {
      notificationsContext.getAllNotifications();
    }
  }, [authContext]);

  const requestPages = {
    society: RequestsPage,
    admin: AdminRequestsPage,
    superAdmin: AdminRequestsPage,
  };
  return (
    <ThemeProvider theme={theme}>
      <MuiPickersUtilsProvider utils={MomentUtils}>
        <Router>
          <Alerts>
            <Switch>
              <Route exact path="/" component={Home} />
              <Route exact path="/about-us" component={AboutUs} />
              <Route exact path="/404" component={PageNotFound} />
              <Route exact path="/login" component={Login} />
              <Route exact path="/forgotpassword" component={ForgotPassword} />
              <Route exact path="/register" component={Register} />
              <Route exact path="/news" component={News} />
              <Route exact path="/resources" component={Resources} />
              <Route exact path="/e-learning" component={Elearning} />
              <Route exact path="/directory" component={Directory} />
              <PrivateRoute exact path="/update" component={SocietyUpdate} />
              <PrivateRoute exact path="/profile" component={UserProfile} />
              <PrivateRoute
                exact
                path="/requests/:id"
                component={requestPages[authContext.userRole] || RequestsPage}
              />
              <PrivateRoute exact path="/society" component={Profile} />
              <PrivateRoute exact path="/society/members" component={Members} />
              <PrivateRoute
                exact
                path="/society/mandatory_returns"
                component={MandatoryReturns}
              />
              <PrivateRoute
                exact
                path="/society/requests"
                component={Requests}
              />
              <PrivateRoute exact path="/society/audits" component={Audits} />
              <PrivateRoute
                exact
                path="/society/consultancy"
                component={Consultancy}
              />
              <PrivateRoute
                exact
                path="/society/financial"
                component={Financial}
              />
              <PrivateRoute exact path="/society/legal" component={Legal} />
              <PrivateRoute
                exact
                path="/notifications"
                component={Notifications}
              />
              <PrivateRoute exact path="/admin" component={Admin} />
              <PrivateRoute
                exact
                path="/admin/analytics"
                component={Analytics}
              />
              <PrivateRoute exact path="/admin/users" component={Users} />
              <PrivateRoute
                exact
                path="/admin/resources"
                component={EditResources}
              />
              <PrivateRoute
                exact
                path="/admin/e-learning"
                component={AdminElearning}
              />
              <PrivateRoute
                exact
                path="/admin/requests"
                component={AdminRequests}
              />
              <PrivateRoute exact path="/auditor" component={AuditMain} />
              <PrivateRoute exact path="/legal" component={LegalMain} />
              <PrivateRoute
                exact
                path="/consultant"
                component={ConsultantMain}
              />
              <PrivateRoute exact path="/financial" component={FinancialMain} />
              <PrivateRoute
                exact
                path="/accounts/requests"
                component={AuditRequests}
              />
              <PrivateRoute exact path="/form/:id" component={FormPage} />
              <Redirect from="*" to="/404" />
            </Switch>
          </Alerts>
        </Router>
      </MuiPickersUtilsProvider>
    </ThemeProvider>
  );
}

export default App;
