import React from "react";

import { Box, Container, Typography, Grid, Button } from "@material-ui/core";
import { grey } from "@material-ui/core/colors";
import { makeStyles } from "@material-ui/core/styles";
import Image from "material-ui-image";
import PageWrapper from "../components/layout/Wrapper/PageWrapper";
import { useHistory } from "react-router-dom";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  greyBox: {
    paddingTop: theme.spacing(10),
    paddingBottom: theme.spacing(10),
    backgroundColor: "#fff",
  },
  topHeading: {
    [theme.breakpoints.down("sm")]: {
      fontSize: "3rem",
    },
  },
  subHeading: {
    color: grey[600],
    marginBottom: theme.spacing(3),
  },
  whiteBox: {
    textAlign: "center",
    // paddingTop: theme.spacing(10),
    paddingBottom: theme.spacing(10),
    backgroundColor: "#fff",
    "& h3": {
      [theme.breakpoints.down("sm")]: {
        fontSize: "2.5rem",
      },
    },
  },
  parallax: {
    backgroundImage:
      'linear-gradient(rgba(255,255,255,0),  rgba(0,0,0,0.8)), url("https://images.unsplash.com/photo-1486406146926-c627a92ad1ab?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=750&q=80")',
    // height: 400,
    backgroundAttachment: "fixed",
    backgroundPosition: "center",
    backgroundRepeat: "no-repeat",
    backgroundSize: "cover",
    paddingTop: theme.spacing(10),
    paddingBottom: theme.spacing(10),
    textAlign: "center",
    color: "#fff",
  },
  servProvBanner: {},
}));

const details = [
  {
    src: "https://static.thenounproject.com/png/2083916-200.png",
    title: "Easy Loans",
    paragraph:
      "Get access to Credit facilities and ease the Loan acquiring process as a member of a Co-operative society.",
  },
  {
    src: "https://static.thenounproject.com/png/945027-200.png",
    title: "Save More",
    paragraph:
      "Members save money When they purchase goods and services in large quantities, as a group.",
  },
  {
    src: "https://static.thenounproject.com/png/2952610-200.png",
    title: "Common Growth",
    paragraph:
      "Societies expand the ability of members to participate in, negotiate with, influence, control, and hold accountable the institutions that affect their businesses.",
  },
];

const Home = () => {
  const classes = useStyles();
  const history = useHistory();
  return (
    <PageWrapper>
      <Box className={classes.greyBox}>
        <Container maxWidth="md">
          <Typography className={classes.topHeading} variant="h1" gutterBottom>
            Getting Ahead Together
          </Typography>
          <Typography className={classes.subHeading} variant="subtitle1">
            Good things can be done by individuals, but Great things can only be
            done with a team. Utilize the power of teamwork to make the relevant
            impact in your industry.
          </Typography>
          <Typography
            className={classes.subHeading}
            variant="subtitle1"
            gutterBottom
          >
            This is a Community of Cooperatives and other stake holders coming
            together to address socio-economic challenges of Nigeria and
            contributing greatly to the GDP of the nation.
          </Typography>
          <Grid container spacing={5}>
            <Grid item xs={12} md={6}>
              <Button
                size="large"
                variant="outlined"
                color="secondary"
                fullWidth
                onClick={() => history.push("/e-learning")}
              >
                Learn More
              </Button>
            </Grid>
            <Grid item xs={12} md={6}>
              <Button
                size="large"
                variant="contained"
                color="secondary"
                fullWidth
                onClick={() => history.push("/register")}
              >
                Join Now
              </Button>
            </Grid>
          </Grid>
        </Container>
      </Box>
      <Box py={15} bgcolor="#ffffff">
        <Container maxWidth="md">
          <Typography
            variant="h3"
            className={classes.topHeading}
            color="primary"
            align="center"
          >
            Cooperative Development and Social Security Management Information
            System
          </Typography>
        </Container>
      </Box>
      <Box className={classes.whiteBox}>
        <Container maxWidth="md">
          <Grid container justify="center" spacing={3}>
            {details.map(({ title, paragraph, src }, index) => {
              return (
                <Grid item xs={12} sm={6} md={4} key={index}>
                  <Image
                    src={src}
                    style={{
                      width: "150px",
                      height: "150px",
                      marginLeft: "auto",
                      marginRight: "auto",
                      padding: "20px",
                      filter: "invert(0%)",
                    }}
                  />
                  <Typography variant="h5" color="initial" gutterBottom>
                    {title}
                  </Typography>
                  <Typography
                    variant="body1"
                    color="initial"
                    className={classes.subHeading}
                  >
                    {paragraph}
                  </Typography>
                </Grid>
              );
            })}
          </Grid>
        </Container>
      </Box>
      <Box className={classes.parallax}>
        <Container maxWidth="md">
          <Typography variant="h3" color="initial" gutterBottom>
            Become a Service Provider
          </Typography>
          <Typography variant="body1" color="initial" gutterBottom>
            Join our team of Auditors, Legal Aid practitioners, Consultants,
            Financial Institutions and other professionals, to get access to a
            wide range of available clients.
          </Typography>
        </Container>
        <Button
          variant="outlined"
          color="secondary"
          style={{ width: "50%", height: 50 }}
          onClick={() => history.push("/register")}
        >
          Register
        </Button>
      </Box>
    </PageWrapper>
  );
};

export default Home;
