import React, { Fragment } from "react";
import { ReactComponent as UpdateSvg } from "../../resources/update_image.svg";
import { ReactComponent as NoFileSvg } from "../../resources/file_search.svg";
import { Typography, Button, Grid, Box } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import { useHistory } from "react-router-dom";

const useStyles = makeStyles((theme) => ({
  svg: {
    marginTop: theme.spacing(1),
    width: 400,
    height: 300,
    [theme.breakpoints.down("sm")]: {
      marginTop: theme.spacing(4),
      width: 250,
      height: (250 * 3) / 4,
    },
  },
}));

const NotApproved = (props) => {
  const classes = useStyles();
  const history = useHistory();
  return (
    <Fragment>
      <Grid
        container
        spacing={1}
        justify="center"
        alignItems="center"
        direction="column"
      >
        <Grid item>
          <UpdateSvg className={classes.svg} />
        </Grid>
        <Grid item>
          <Box>
            <Typography
              align="center"
              variant="h4"
              color="secondary"
              gutterBottom
            >
              Account Not Approved!
            </Typography>
            <Typography
              align="center"
              variant="body2"
              color="initial"
              gutterBottom
            >
              This account has not been approved yet. If you have not requested
              for a profile update, click the Button below
            </Typography>
          </Box>
        </Grid>
        <Grid item>
          <Button
            variant="contained"
            color="secondary"
            onClick={() => {
              history.push("/update");
            }}
          >
            Update Profile
          </Button>
        </Grid>
      </Grid>
    </Fragment>
  );
};

export const FileEmpty = (props) => {
  const classes = useStyles();
  return (
    <Fragment>
      <Grid
        container
        spacing={1}
        justify="center"
        alignItems="center"
        direction="column"
      >
        <Grid item>
          <NoFileSvg className={classes.svg} />
        </Grid>
        <Grid item>
          <Box>
            <Typography
              align="center"
              variant="h4"
              color="secondary"
              gutterBottom
            >
              {props.title}
            </Typography>
            <Typography
              align="center"
              variant="body2"
              color="initial"
              gutterBottom
            >
              {props.body}
            </Typography>
          </Box>
        </Grid>
      </Grid>
    </Fragment>
  );
};

export default NotApproved;
