import React, { Fragment, useContext, useEffect } from "react";
import { IconButton, Tooltip } from "@material-ui/core";
import NotificationsContext from "../components/context/notifications/notificationsContext";
import Titles from "../components/layout/Titles";
import DashboardWrapper from "../components/layout/Wrapper/DashboardWrapper";
import Notices from "../components/layout/Notifications/Notifications";
import { DoneAllRounded } from "@material-ui/icons";

const Notifications = (props) => {
  const { readAllNotification, newNotifications } = useContext(
    NotificationsContext
  );
  return (
    <DashboardWrapper selectedIndex={0}>
      <Titles title="Notifications">
        <Tooltip title="Mark all read">
          <IconButton
            onClick={readAllNotification}
            disabled={!(newNotifications.length > 0)}
            color="secondary"
          >
            <DoneAllRounded />
          </IconButton>
        </Tooltip>
      </Titles>
      <Notices />
    </DashboardWrapper>
  );
};

export default Notifications;
