import React, { useState, useContext, useEffect } from "react";
import {
  Box,
  Container,
  Typography,
  TextField,
  Button,
  CircularProgress,
  Grid,
  FormControlLabel,
  Checkbox,
} from "@material-ui/core";
import PageWrapper from "../components/layout/Wrapper/PageWrapper";
import PasswordObj, { SelectObj } from "../components/layout/FormItems";
import { makeStyles } from "@material-ui/core/styles";
import AlertContext from "../components/context/alert/alertContext";
import AuthContext from "../components/context/auth/authContext";

import Dashboard from "../components/routing/Dashboard";
const useStyles = makeStyles((theme) => ({
  root: { paddingTop: theme.spacing(5), paddingBottom: theme.spacing(5) },
  textField: {
    width: "-webkit-fill-available",
  },
}));

const Register = (props) => {
  const alertContext = useContext(AlertContext);
  const authContext = useContext(AuthContext);

  const [agree, setAgree] = useState(false);

  const { setAlert } = alertContext;

  const {
    register,
    error,
    clearErrors,
    isAuthenticated,
    loading,
  } = authContext;

  const classes = useStyles();
  const [user, setUser] = useState({
    name: "",
    email: "",
    password: "",
    password2: "",
    role: "society",
  });
  const usernameRegex = /^[a-zA-Z0-9.*-+@#?!]+$/;
  const { name, email, password, password2, role } = user;
  const onChange = (e) => {
    setUser({
      ...user,
      [e.target.id || e.target.name]: e.target.value,
    });
    if (e.target.id === "name" && e.target.value.length === 0)
      setAlert("Invalid account name", "error");
  };
  const options = [
    { value: "society", label: "Society" },
    { value: "extauditor", label: "General Auditor" },
    { value: "intauditor", label: "Government Auditor" },
    { value: "legal", label: "Legal Practitioner" },
    { value: "financial", label: "Financial Institution" },
    { value: "consultant", label: "Consultant" },
  ];

  useEffect(() => {
    if (error) {
      setAlert(error, "error");
      clearErrors();
    }
    // eslint-disable-next-line
  }, [error]);
  const page = Dashboard();
  useEffect(() => {
    if (isAuthenticated && !loading) props.history.push(page);
    // eslint-disable-next-line
  }, [isAuthenticated, loading]);

  const onSubmit = (e) => {
    e.preventDefault();
    if (
      name === "" ||
      email === "" ||
      password === "" ||
      password2 === "" ||
      role === ""
    ) {
      setAlert("Please fill in all fields", "error");
    } else if (password !== password2) {
      setAlert("Passwords do not match", "error");
    } else if (password.length < 8) {
      setAlert(
        `Minimum password length is 8, you gave ${
          password.length.toFixed() +
          " " +
          (password.length > 1 ? "characters" : "character")
        } `,
        "warning"
      );
    } else {
      register({
        name,
        email,
        password,
        role,
      });
    }
  };
  return (
    <PageWrapper noLoad>
      <Box bgcolor="background.paper" py={12}>
        <Container maxWidth="sm">
          <Grid container spacing={3}>
            <Box display={{ xs: "block", md: "flex" }} justifyContent="center">
              <Typography component="span" variant="h3" color="initial">
                Account{" "}
              </Typography>
              <Typography component="span" variant="h3" color="secondary">
                Register
              </Typography>
            </Box>
            <Grid item xs={12}>
              <TextField
                id="name"
                label="Username"
                variant="outlined"
                color="primary"
                margin="normal"
                size="medium"
                value={name}
                onChange={onChange}
                className={classes.textField}
                required
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                id="email"
                label="Email"
                variant="outlined"
                color="primary"
                margin="normal"
                size="medium"
                value={email}
                onChange={onChange}
                className={classes.textField}
                required
              />
            </Grid>
            <Grid item xs={6}>
              <PasswordObj
                id="password"
                password={password}
                label="Password"
                onChange={onChange}
                className={classes.textField}
                minLength="6"
                required
              />
            </Grid>
            <Grid item xs={6}>
              <PasswordObj
                id="password2"
                value={password2}
                label="Confirm"
                onChange={onChange}
                className={classes.textField}
                minLength="6"
                required
              />
            </Grid>
            <Grid item xs={12}>
              <SelectObj
                id="role"
                label="Account Type"
                value={role}
                onChange={onChange}
                defaultValue="society"
                options={options}
                className={classes.textField}
                required
              />
            </Grid>
            <Grid item xs={12}>
              <FormControlLabel
                control={
                  <Checkbox
                    color="primary"
                    checked={agree}
                    onChange={() => {
                      setAgree(!agree);
                    }}
                  />
                }
                label="I Agree to the terms and conditions"
              />
            </Grid>
            <Grid item xs={12}>
              <Button
                variant="contained"
                color="primary"
                className={classes.textField}
                size="large"
                onClick={onSubmit}
                disabled={loading || !agree}
              >
                {loading ? (
                  <CircularProgress size={25} color="contrastText.primary" />
                ) : (
                  "Submit"
                )}
              </Button>
            </Grid>
          </Grid>
        </Container>
      </Box>
    </PageWrapper>
  );
};

export default Register;
