import {
  GET_FILES,
  POST_FILES,
  DELETE_FILE,
  FILE_ERROR,
  CLEAR_ERROR,
  RESOURCE_ERROR,
  GET_RESOURCES,
} from "../types";

export default (state, action) => {
  switch (action.type) {
    case GET_FILES:
      return {
        ...state,
        files: action.payload,
      };
    case CLEAR_ERROR:
      return {
        ...state,
        fileError: null,
      };
    case FILE_ERROR:
      return {
        ...state,
        fileError: action.payload,
        // loading: false,
      };
    case GET_RESOURCES:
      return {
        ...state,
        resources: action.payload,
      };
    case RESOURCE_ERROR:
    case POST_FILES:
    case DELETE_FILE:
    default:
      return;
  }
};
