import React, { useState, useEffect, Fragment, useContext } from "react";
import Dropzone from "react-dropzone";
import {
  Dialog,
  DialogContent,
  DialogTitle,
  Button,
  Typography,
  Box,
  Grid,
  IconButton,
} from "@material-ui/core";
import { CloseRounded } from "@material-ui/icons";
import { makeStyles } from "@material-ui/core/styles";
import AlertContext from "../context/alert/alertContext";

const useStyles = makeStyles((theme) => ({
  files: {
    maxHeight: 200,
    overflowY: "scroll",
    backgroundColor: "yellow",
  },
  dragwindow: {
    flex: 1,
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    padding: "20px",
    borderWidth: 3,
    borderRadius: 5,
    borderColor: "#eeeeee",
    borderStyle: "dashed",
    backgroundColor: "#fafafa",
    color: "#bdbdbd",
    outline: "none",
    transition: "border .24s ease-in-out",
    fontSize: 18,
  },
}));

const Upload = (props) => {
  const alertContext = useContext(AlertContext);
  const [file, setFile] = useState(null);
  const { setAlert } = alertContext;
  const { open, onDrop, setOpen } = props;
  const classes = useStyles();
  const maxSize = 26 * 1048576;
  useEffect(() => {
    if (open) setFile(null);
  }, [open]);
  return (
    <Dialog
      open={open}
      onClose={() => {
        setOpen(false);
      }}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
      fullWidth
    >
      <DialogContent>
        <Box mb={3}>
          <Box display="flex" justifyContent="flex-end">
            <IconButton
              size="small"
              onClick={() => {
                setOpen(false);
              }}
            >
              <CloseRounded fontSize="small" />
            </IconButton>
          </Box>
          <Grid container spacing={3}>
            <Grid item xs={12} md={8}>
              <Dropzone
                onDrop={onDrop}
                accept="application/pdf, application/doc "
                maxSize={maxSize}
                minSize={0}
                maxFiles={1}
                multiple={false}
                onDropRejected={(rejected) => {
                  console.log(rejected);
                  if (rejected[0].file.size > maxSize) {
                    setAlert("File too big!", "error");
                  } else {
                    setAlert("File type not allowed", "error");
                  }
                  setFile(null);
                }}
                onDropAccepted={(acceptedFiles) => {
                  setAlert("File Collected", "success");
                  setFile(acceptedFiles[0]);
                }}
              >
                {({
                  getRootProps,
                  getInputProps,
                  isDragActive,
                  isDragReject,
                  isDragAccept,
                  acceptedFiles,
                  rejectedFiles,
                }) => {
                  if (file) {
                    console.log(file);
                  }
                  return (
                    <div {...getRootProps()} className={classes.dragwindow}>
                      <Box
                        height={200}
                        display="flex"
                        alignItems="center"
                        alignContent="center"
                      >
                        <input {...getInputProps()} />
                        {!isDragActive &&
                          "Click here or drop a file to upload!"}
                        {isDragActive &&
                          !isDragReject &&
                          "Drop it like it's hot!"}
                        {isDragReject && "File type not accepted, sorry!"}
                      </Box>
                    </div>
                  );
                }}
              </Dropzone>
            </Grid>
            <Grid item xs={12} md={4}>
              <Box height="100%">
                <Typography variant="subtitle1" color="initial">
                  Files
                </Typography>
              </Box>
            </Grid>
          </Grid>
        </Box>
      </DialogContent>
    </Dialog>
  );
};

export const UploadButton = (props) => {
  const { text } = props;
  const [open, setOpen] = useState(false);
  const handleOpen = () => {
    setOpen(true);
  };
  return (
    <Fragment>
      <Button variant="outlined" color="secondary" onClick={handleOpen}>
        {text}
      </Button>
      <Upload open={open} setOpen={setOpen} />
    </Fragment>
  );
};

export default Upload;
