import React, { useState, useContext, useEffect } from "react";
import PropTypes from "prop-types";
import {
  AppBar,
  Toolbar,
  IconButton,
  Typography,
  Icon,
  CssBaseline,
  useScrollTrigger,
  InputBase,
  Slide,
  Menu,
  Hidden,
  MenuItem,
  Tooltip,
  Divider,
  Link,
  Button,
  Box,
} from "@material-ui/core";
import { fade, makeStyles } from "@material-ui/core/styles";
import { useHistory } from "react-router-dom";
import {
  MoreVertRounded,
  IndeterminateCheckBoxSharp,
} from "@material-ui/icons";
import AuthContext from "../../context/auth/authContext";

import Dashboard from "../../routing/Dashboard";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  navitems: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
  },
  title: {
    flexGrow: 1,
    textAlign: "left",
    display: "none",
    textDecoration: "none",
    color: "#fff",
    cursor: "pointer",
    [theme.breakpoints.up("sm")]: {
      display: "block",
    },
  },
  search: {
    position: "relative",
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.white, 0.15),
    "&:hover": {
      backgroundColor: fade(theme.palette.common.white, 0.25),
    },
    marginLeft: 0,
    width: "100%",
    [theme.breakpoints.up("sm")]: {
      marginLeft: theme.spacing(1),
      width: "auto",
    },
  },
  searchIcon: {
    padding: theme.spacing(0, 2),
    height: "100%",
    position: "absolute",
    pointerEvents: "none",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  inputRoot: {
    color: "inherit",
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
    transition: theme.transitions.create("width"),
    width: "100%",
    [theme.breakpoints.up("sm")]: {
      width: "12ch",
      "&:focus": {
        width: "20ch",
      },
    },
  },
  menuText: {
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(2),
    color: theme.palette.primary.contrastText,
    opacity: "80%",
    "&:hover": {
      opacity: "100%",
    },
  },
}));

const NavMenuItem = (props) => {
  return (
    <Button {...props} color="inherit">
      {props.text}
    </Button>
  );
};
const menu = [
  { text: "About us", to: "/about-us" },
  { text: "Resources", to: "/resources" },
  // { text: "News", to: "/news" },
  { text: "E-learning", to: "/e-learning" },
  { text: "Social Security", to: "/login" },
  { text: "Interstate Directories", to: "/directory" },
];

function HideOnScroll(props) {
  const { children, window } = props;
  // Note that you normally won't need to set the window ref as useScrollTrigger
  // will default to window.
  // This is only being set here because the demo is in an iframe.
  const trigger = useScrollTrigger({ target: window ? window() : undefined });

  return (
    <Slide appear={false} direction="down" in={!trigger}>
      {children}
    </Slide>
  );
}

HideOnScroll.propTypes = {
  children: PropTypes.element.isRequired,
  /**
   * Injected by the documentation to work in an iframe.
   * You won't need it on your project.
   */
  window: PropTypes.func,
};

const Navbar = (props) => {
  const authContext = useContext(AuthContext);
  const classes = useStyles();
  const dashboardLink = Dashboard();
  const [anchorEl, setAnchorEl] = useState(null);
  const { isAuthenticated, loadUser, loading } = authContext;

  const menuOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const menuClose = () => {
    setAnchorEl(null);
  };
  const iconstyle = {
    color: "#fff",
  };
  const history = useHistory();
  const goto = (to) => {
    history.push(to);
  };
  return (
    <>
      <CssBaseline />
      <HideOnScroll {...props}>
        <AppBar>
          <Toolbar>
            <Hidden xsDown>
              <Box
                onClick={() => goto("/")}
                display="flex"
                flexDirection="row"
                alignItems="center"
                style={{ flexGrow: 1 }}
              >
                <img
                  src="../../coat512.png"
                  height={50}
                  width={50}
                  style={{ marginRight: 5 }}
                />
                <Typography variant="h6" noWrap>
                  Ministry of Labour and Employment
                </Typography>
              </Box>
            </Hidden>
            <Hidden smUp>
              <img
                onClick={() => goto("/")}
                src="../../coat512.png"
                height={50}
                width={50}
                style={{ marginRight: 5 }}
              />
            </Hidden>

            <div className={classes.search}>
              <div className={classes.searchIcon}>
                <Icon className="material-icons">search</Icon>
              </div>
              <InputBase
                placeholder="Search…"
                classes={{
                  root: classes.inputRoot,
                  input: classes.inputInput,
                }}
                inputProps={{ "aria-label": "search" }}
              />
            </div>
            <Hidden className={classes.menuItemLink} mdDown>
              {menu.map(({ text, to }, index) => {
                return (
                  <NavMenuItem
                    text={text}
                    className={classes.menuText}
                    onClick={() => goto(to)}
                    key={index}
                  />
                );
              })}
              {isAuthenticated ? (
                <NavMenuItem
                  text="Dashboard"
                  className={classes.menuText}
                  onClick={() => goto(dashboardLink)}
                />
              ) : (
                [
                  <NavMenuItem
                    text="Register"
                    className={classes.menuText}
                    onClick={() => goto("/register")}
                    key="register"
                  />,
                  <NavMenuItem
                    text="Login"
                    className={classes.menuText}
                    onClick={() => goto("/login")}
                    key="login"
                  />,
                ]
              )}
            </Hidden>
            <Hidden lgUp>
              <Tooltip title="More">
                <IconButton
                  style={iconstyle}
                  aria-label="more-options"
                  onClick={menuOpen}
                >
                  <MoreVertRounded />
                </IconButton>
              </Tooltip>

              <Menu
                id="simple-menu"
                anchorEl={anchorEl}
                keepMounted
                open={Boolean(anchorEl)}
                onClose={menuClose}
                onClick={menuClose}
                className={classes.dropDownMenuLink}
              >
                <MenuItem onClick={() => goto("/")}>Home</MenuItem>
                {menu.map(({ text, to }, index) => {
                  return (
                    <MenuItem key={index} onClick={() => goto(to)}>
                      {text}
                    </MenuItem>
                  );
                })}

                <Divider />
                {isAuthenticated ? (
                  <MenuItem onClick={() => goto(dashboardLink)}>
                    Dashboard
                  </MenuItem>
                ) : (
                  [
                    <MenuItem onClick={() => goto("/register")}>
                      Register
                    </MenuItem>,
                    <MenuItem onClick={() => goto("/login")}>Login</MenuItem>,
                  ]
                )}
              </Menu>
            </Hidden>
          </Toolbar>
        </AppBar>
      </HideOnScroll>
      <Toolbar />
    </>
  );
};

export default Navbar;
