import React, { useContext, useEffect, createRef } from "react";
import AlertContext from "../context/alert/alertContext";
import { SnackbarProvider } from "notistack";

const Alerts = (props) => {
  const alertContext = useContext(AlertContext);
  const providerRef = createRef();
  useEffect(() => {
    alertContext.alert.msg !== "" &&
      alertContext.alert.type !== "" &&
      providerRef.current.enqueueSnackbar(alertContext.alert.msg, {
        variant: alertContext.alert.type,
        preventDuplicate: true,
      });
  }, [alertContext, providerRef]);

  return (
    <SnackbarProvider ref={providerRef} maxSnack={4}>
      {props.children}
    </SnackbarProvider>
  );
};

export default Alerts;
