import React, { useState, useContext, useEffect } from "react";
import { Link as RouterLink } from "react-router-dom";
import {
  TextField,
  Button,
  Typography,
  Grid,
  Box,
  Container,
  CircularProgress,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import PageWrapper from "../components/layout/Wrapper/PageWrapper";

import AlertContext from "../components/context/alert/alertContext";
import AuthContext from "../components/context/auth/authContext";

import Dashboard from "../components/routing/Dashboard";
import Axios from "axios";

const useStyles = makeStyles((theme) => ({
  root: { paddingTop: theme.spacing(10), paddingBottom: theme.spacing(10) },
  margin: {
    margin: theme.spacing(1),
  },
  links: {
    textDecoration: "none",
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(2),
  },
  textField: {
    width: "-webkit-fill-available",
  },
}));

const ForgotPassword = (props) => {
  const classes = useStyles();
  const [email, setEmail] = useState("");

  const alertContext = useContext(AlertContext);
  const authContext = useContext(AuthContext);
  const { setAlert } = alertContext;
  const { isAuthenticated, loading, error, getPath } = authContext;
  const [load, setLoad] = useState(false);

  const reset = async () => {
    setLoad(true);
    try {
      const res = await Axios.get(`api/users/forgotpassword/${email}`);
      console.log(res);
      setAlert("Reset Information sent to your email", "success");
      props.history.push("/login");
    } catch (error) {
      setAlert(
        (error.response &&
          ((error.response.data && error.response.data.msg) ||
            error.response.statusText)) ||
          "Runtime Error",
        "error"
      );
    }
    setLoad(false);
  };

  const resetClick = () => {
    if (email === "") {
      setAlert("Please provide a valid Email", "error");
    } else {
      reset();
    }
  };

  const page = Dashboard();
  useEffect(() => {
    if (error) {
      setAlert(error, "error");
    } else if (isAuthenticated && !loading) {
      props.history.push(getPath() || page);
    }
  }, [isAuthenticated, loading, props.history, error]);

  return (
    <PageWrapper>
      <Box bgcolor="background.paper" py={12}>
        <Container maxWidth="sm">
          <Grid
            container
            justify="center"
            spacing={3}
            direction="column"
            alignItems="stretch"
          >
            <Grid container justify="center">
              <Typography variant="h3" color="secondary" gutterBottom>
                Forgot Password
              </Typography>
            </Grid>

            <Grid item>
              <TextField
                id="email"
                className={classes.textField}
                label="Email"
                value={email}
                onChange={(e) => {
                  setEmail(e.target.value);
                }}
                variant="outlined"
                required
              />
            </Grid>

            <Grid item>
              <Button
                variant="contained"
                color="primary"
                size="large"
                fullWidth
                onClick={resetClick}
                disabled={loading || load}
              >
                {loading || load ? (
                  <CircularProgress size={25} color="inherit" />
                ) : (
                  "Send Reset Email"
                )}
              </Button>
            </Grid>
            <Grid container direction="row" justify="center">
              <Grid item>
                <Typography
                  className={classes.links}
                  variant="caption"
                  color="primary"
                  component={RouterLink}
                  to="/login"
                >
                  Back to Login
                </Typography>
              </Grid>
            </Grid>
          </Grid>
        </Container>
      </Box>
    </PageWrapper>
  );
};

export default ForgotPassword;
